import styled from "@emotion/styled";
import {
  Button,
} from "@mui/material";
import Box from "@mui/material/Box";
import { useState } from "react";
import { SequentialTrigger, System, Trigger } from "../../../system/interfaces";
import { TriggerInfo } from "./TriggerInfo";
import { TriggeredInfo } from "./TriggeredInfo";

const Root = styled(Box)`
  display: flex;
  flex: 1 1 auto;
  flex-direction: row;
  margin: 4px;
  padding: 16px;
`;

const EventListBox = styled(Box)`
  bottom: 0;
  min-width: 200px;
  display: flex;
  flex-direction: column;
`;

const EventList = styled(Box)`
  display: flex;
  flex: 1;
  flex-direction: column;
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-radius: 4px;
  overflow: scroll;
  padding: 8px;
  margin: 8px;
`;

export function CanBusEventsConfiguration({
  system,
  onChange,
}: CanBusEventsConfigurationProperty) {
  const [selectedEvent, selectEvent] = useState(1);
  const [selectedSequence, selectSequence] = useState(0);
  const [selectedCondition, selectCondition] = useState(-1);

  return (
    <Root>
      <CanBusEventsPanel
        numberOfEvents={system.can_events.length}
        onEventSelect={selectEvent}
        selectedEvent={selectedEvent}
        items={system.can_events}
      />
      <TriggerInfo
        trigger={system.can_events.at(selectedEvent - 1)}
        numberOfEvent={system.number_of_can_events}
        numberOfKey={system.number_of_keys}
        selectedSequence={selectedSequence}
        onSequenceSelect={selectSequence}
        selectedCondition={selectedCondition}
        onConditionSelect={selectCondition}
        onChange={(t) => {
          system.can_events[selectedEvent - 1] = t;
          onChange({ ...system });
        }}
      />
      <Box
        sx={{
          width: "300px",
          // border: '1px solid rgba(0, 0, 0, 0.12)',
          borderRadius: "4px",
          margin: "0 16px",
          flex: "4",
          display: "flex",
        }}
      >
        <TriggeredInfo
          system={system}
          selectedTrigger={selectedEvent - 1}
          selectedSequence={selectedSequence}
          selectedCondition={selectedCondition}
          onChange={(system) => onChange({ ...system })}
        />
      </Box>
    </Root>
  );
}

function CanBusEventsPanel({
  numberOfEvents,
  onEventSelect,
  selectedEvent,
  items,
}: EventListProperty) {
  return (
    <EventListBox>
      <EventList>
        {[...Array(numberOfEvents).keys()].map((i) => (
          <EventButton
            index={items[i].id}
            name={`${(items[i] as Trigger).name ?? "Sequential"}`}
            onClick={() => onEventSelect(items[i].id)}
            isSelected={selectedEvent === items[i].id}
          />
        ))}
      </EventList>
    </EventListBox>
  );
}

function EventButton({
  index,
  name,
  onClick,
  isSelected = false,
}: EventButtonProperty) {
  return (
    <Button
      variant="outlined"
      sx={{
        display: "flex",
        flexDirection: "column !important",
        alignItems: "stretch",
        margin: "4px",
        backgroundColor: isSelected ? "rgba(25, 118, 210, 0.5)" : "",
      }}
      onClick={(_) => !!onClick && onClick()}
    >
      <Box
        sx={{
          display: "flex",
          justifyContent: "space-around",
        }}
      >
        {`${name} (${index})`}
      </Box>
    </Button>
  );
}

interface CanBusEventsConfigurationProperty {
  system: System;
  onChange(value: System): void;
}

interface EventListProperty {
  numberOfEvents: number;
  onEventSelect: Function;
  selectedEvent?: number;
  items: Trigger[] | SequentialTrigger[];
}

interface EventButtonProperty {
  index: number;
  name: string;
  onClick?: Function;
  isSelected?: boolean;
}
