import MPaper from "@mui/material/Paper";
import styled from "@emotion/styled";
import Box from "@mui/material/Box";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import React, {useState} from "react";
import {System} from "../../system/interfaces";
import {KeysConfiguration} from "./KeysConfiguration";
import {VirtualKeysConfiguration} from "./virtual_keys/VirtualKeysConfiguration";
import {InputsConfiguration} from "./inputs/InputsConfiguration";
import {CanBusEventsConfiguration} from "./can_bus/CanBusEventsConfiguration";
import {HornRingsConfiguration} from "./horn-ring/HornRingConfiguration";
import {GSensorConfiguration} from "./g_sensor/GSensorConfiguration";
import {TemperatureConfiguration} from "./temperature/TemperatureConfiguration";
import {AnalogInputsConfiguration} from "./analog_input/AnalogInputConfiguration";

const Paper = styled(MPaper)`
  border: 1px solid rgb(0, 0, 0, 0.2);
  margin: 16px;
  max-height: 65vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
`

const TabBox = styled(Box)`
  border-bottom: 1px;
  border-bottom-style: solid;
  border-bottom-color: rgb(0, 0, 0, 0.12);
  width: 100%;
  height: fit-content;
`

export function ConfigurationPanel ({ system, onChange }: ConfigurationPanelProperty) {
  const [selectedTab, selectTab] = useState(0);
  return (
    <Paper variant="outlined">
      <TabBox>
        <Tabs
          value={selectedTab}
          onChange={(_, value) => selectTab(value)}
          variant="fullWidth"
          centered
        >
          <Tab label="Keys" />
          <Tab label="Inputs" />
          <Tab disabled={!system.info?.canBus?.modelId} label="CAN Events" />
          <Tab label="Horn Ring" />
          <Tab label="Virtual Keys" />
          <Tab label="G Sensor" />
          <Tab label="Temperature" />
          <Tab label="Analog Inputs" />
        </Tabs>
      </TabBox>
      <TabPanel value={0} selectedTab={selectedTab}>
        <KeysConfiguration system={system} onChange={onChange} />
      </TabPanel>
      <TabPanel value={1} selectedTab={selectedTab}>
        <InputsConfiguration system={system} onChange={onChange} />
      </TabPanel>
      <TabPanel value={2} selectedTab={selectedTab}>
        <CanBusEventsConfiguration system={system} onChange={onChange} />
      </TabPanel>
      <TabPanel value={3} selectedTab={selectedTab}>
        <HornRingsConfiguration system={system} onChange={onChange} />
      </TabPanel>
      <TabPanel value={4} selectedTab={selectedTab}>
        <VirtualKeysConfiguration system={system} onChange={onChange} />
      </TabPanel>
      <TabPanel value={5} selectedTab={selectedTab}>
        <GSensorConfiguration system={system} onChange={onChange} />
      </TabPanel>
      <TabPanel value={6} selectedTab={selectedTab}>
        <TemperatureConfiguration system={system} onChange={onChange} />
      </TabPanel>
      <TabPanel value={7} selectedTab={selectedTab}>
        <AnalogInputsConfiguration system={system} onChange={onChange} />
      </TabPanel>
    </Paper>
  );
}

function TabPanel({ value, selectedTab, children }: TabPanelProperty){
  if (value === selectedTab) {
    return <Box
      sx={{
        display: 'flex',
        flex: 1,
        overflowY: 'hidden',
        overflowX: 'scroll',
      }}
  >{children}</Box>
  }

  return <div></div>
}

export interface ConfigurationPanelProperty {
  system: System,
  onChange(value: System): void,
}

interface TabPanelProperty {
  value: number,
  selectedTab: number,
    children?: React.ReactNode,
}
