import styled from "@emotion/styled";
import {
  Button,
} from "@mui/material";
import Box from "@mui/material/Box";
import { useState } from "react";
import { SequentialTrigger, System, Trigger } from "../../../system/interfaces";
import { TriggerInfo } from "./TriggerInfo";
import { TriggeredInfo } from "./TriggeredInfo";

const Root = styled(Box)`
  display: flex;
  flex: 1 1 auto;
  flex-direction: row;
  margin: 4px;
  padding: 16px;
`;

const GSensorListBox = styled(Box)`
  bottom: 0;
  min-width: 200px;
  display: flex;
  flex-direction: column;
`;

const GSensorList = styled(Box)`
  display: flex;
  flex: 1;
  flex-direction: column;
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-radius: 4px;
  overflow: scroll;
  padding: 8px;
  margin: 8px;
`;

export function GSensorConfiguration({
  system,
  onChange,
}: GSensorConfigurationProperty) {
  const [selectedGSensor, selectGSensor] = useState(13);
  const [selectedSequence, selectSequence] = useState(0);
  const [selectedCondition, selectCondition] = useState(-1);

  return (
    <Root>
      <GSensorPanel
        numberOfGSensor={system.inputs.length}
        onGSensorSelect={selectGSensor}
        selectedGSensor={selectedGSensor}
        items={system.inputs}
      />
      <TriggerInfo
        trigger={system.inputs.at(selectedGSensor - 1)}
        numberOfKey={system.number_of_keys}
        numberOfEvent={system.number_of_can_events}
        selectedSequence={selectedSequence}
        onSequenceSelect={selectSequence}
        selectedCondition={selectedCondition}
        onConditionSelect={selectCondition}
        onChange={(t) => {
          system.inputs[selectedGSensor - 1] = t;
          onChange({ ...system });
        }}
      />
      <Box
        sx={{
          width: "300px",
          // border: '1px solid rgba(0, 0, 0, 0.12)',
          borderRadius: "4px",
          margin: "0 16px",
          flex: "4",
          display: "flex",
        }}
      >
        <TriggeredInfo
          system={system}
          selectedTrigger={selectedGSensor - 1}
          selectedSequence={selectedSequence}
          selectedCondition={selectedCondition}
          onChange={(system) => onChange({ ...system })}
        />
      </Box>
    </Root>
  );
}

function GSensorPanel({
  numberOfGSensor,
  onGSensorSelect,
  selectedGSensor,
  items,
}: GSensorListProperty) {
  return (
    <GSensorListBox
      sx={{
        display: 'none !important',
      }}
    >
      <GSensorList>
        {[...Array(numberOfGSensor).keys()].map((i) => (
          <GSensorButton
            index={items[i].id}
            name={`${(items[i] as Trigger).name ?? "Sequential"}`}
            onClick={() => onGSensorSelect(items[i].id)}
            isSelected={selectedGSensor === items[i].id}
          />
        ))}
      </GSensorList>
    </GSensorListBox>
  );
}

function GSensorButton({
  index,
  name,
  onClick,
  isSelected = false,
}: GSensorButtonProperty) {
  return (
    <Button
      key={index}
      variant="outlined"
      sx={{
        display: "flex",
        flexDirection: "column !important",
        alignItems: "stretch",
        margin: "4px",
        backgroundColor: isSelected ? "rgba(25, 118, 210, 0.5)" : "",
      }}
      onClick={(_) => !!onClick && onClick()}
    >
      <Box
        sx={{
          display: "flex",
          justifyContent: "space-around",
        }}
      >
        {`${name} (${index})`}
      </Box>
    </Button>
  );
}

interface GSensorConfigurationProperty {
  system: System;
  onChange(value: System): void;
}

interface GSensorListProperty {
  numberOfGSensor: number;
  onGSensorSelect: Function;
  selectedGSensor?: number;
  items: Trigger[] | SequentialTrigger[];
}

interface GSensorButtonProperty {
  index: number;
  name: string;
  onClick?: Function;
  isSelected?: boolean;
}
