export interface TriggerInfoProperty {
  trigger?: Trigger | SequentialTrigger;
  selectedSequence: number;
  selectedCondition: number;
  numberOfEvent: number;
  numberOfKey: number;
  onChange(value: Trigger | SequentialTrigger): void;
  onSequenceSelect(value: number): void;
  onConditionSelect(value: number): void;
}

export interface SequentialTriggerInfoProperty {
  trigger: SequentialTrigger;
  selectedSequence: number;
  onChange(value: SequentialTrigger): void;
  onSequenceSelect(value: number): void;
}

export interface NonSequentialTriggerInfoProperty {
  trigger: Trigger;
  numberOfEvent: number;
  numberOfKey: number;
  selectedCondition: number;
  onChange(value: Trigger): void;
  onConditionSelect(value: number): void;
}

export interface SequenceInfoProperty {
  sequence?: Sequence;
  onChange(value: Sequence): void;
}

