import {useState} from "react";
import {selectController, setController} from "../../../app/appSlice";
import {useAppDispatch, useAppSelector} from "../../../app/hooks";
import {Controller} from "../interfaces";
import {Image, ImageButton, Root} from "./styles";

export const controllers: Controller[] = [
  {
    id: 'iq_master',
    name: 'IQ Master Box',
    image: './iq/controllers/iq-master-box.png',
    config: {
      number_of_inputs: 12,
      has_photocell: 0,
      photocell_sensitivity: 0,
    }
  },
  {
    id: 'iq_master_pro',
    name: 'IQ Master Pro Box',
    image: './iq/controllers/iq-pro-master-box.png',
    config: {
      number_of_inputs: 12,
      has_photocell: 0,
      photocell_sensitivity: 0,
    }
  },
];

export function ControllerSelector(){
  const selectedController = useAppSelector(selectController);
  const dispatch = useAppDispatch();

  return (
    <Root id="controller-selector-root" key="ControllerSelector">
      {
        controllers.map(c =>
        <ImageButton
          value={c}
          selected={selectedController?.id === c.id}
          onChange={() => {
            dispatch(setController(c));
          }}
        >
          <Image src={`${c.image}`}/>
          <h5>{c.name}</h5>
        </ImageButton>
        )
      }
    </Root>
  );
}
