import styled from "@emotion/styled";
import { MenuItem, TextField } from "@mui/material";
import { useState } from "react";
import { selectVehicle, setVehicle } from "../../../app/appSlice";
import { useAppDispatch, useAppSelector } from "../../../app/hooks";
import { VehicleBrand, VehicleManufactureYear } from "../interfaces";
import { Root } from "./styles";

const brands: VehicleBrand[] = [
  {
    id: "ford",
    name: "Ford",
    value: 1,
    manufactureYears: [
      {
        year: 2021,
        models: [
          {
            id: "explorer",
            name: "Explorer",
            canEvents: 10,
          },
        ],
      },
    ],
  },
  {
    id: "chevrolet",
    name: "Chevrolet",
    value: 2,
    manufactureYears: [
      {
        year: 2021,
        models: [
          {
            id: "tahoe",
            name: "Tahoe",
            canEvents: 10,
          },
        ],
      },
    ],
  },
];

const Select = styled(TextField)`
  min-width: 125px;
  width: 30vw;
`;

export function CanBusSelector() {
  const defaultCanBus = {
    brandId: "",
    mfy: 0,
    modelId: "",
    events: 0,
  };
  const selectedVehicle = useAppSelector(selectVehicle) ?? {...defaultCanBus};
  const dispatch = useAppDispatch();
  const [isCanBusEnable, enableCanBus] = useState(false);

  return (
    <Root
      id="can-bus-selector"
      key="CanBus"
      sx={{
        flexDirection: "column",
      }}
    >
      <Select
        id="can-bus-enable"
        select
        label="Is CAN BUS available"
        value={isCanBusEnable ? 1 : 0}
      >
          <MenuItem
            value={0}
            selected={!isCanBusEnable}
            onClick={() => {
              enableCanBus(false);
              dispatch(setVehicle({...defaultCanBus}))
            }
            }
          >
            No
          </MenuItem>
          <MenuItem
            value={1}
            selected={!isCanBusEnable}
            onClick={() => enableCanBus(true)}
          >
            Yes
          </MenuItem>
      </Select>
      <Select
        id="can-bus-brand"
        select
        helperText="Please select the vehicle brand"
        value={getBrand(selectedVehicle?.brandId)?.value ?? ""}
        disabled={!isCanBusEnable}
      >
        {brands.map((brand) => (
          <MenuItem
            value={brand.value}
            key={brand.value}
            selected={selectedVehicle?.brandId === brand.id}
            onClick={() => {
              const newState = structuredClone(selectedVehicle);
              newState.brandId = brand.id;
              newState.mfy = 0;
              newState.modelId = "";

              dispatch(setVehicle(newState));
            }}
          >
            {brand.name}
          </MenuItem>
        ))}
      </Select>
      <Select
        id="can-bus-year"
        select
        helperText="Please select the vehicle manufacture year"
        value={selectedVehicle.mfy}
        disabled={!isCanBusEnable || !selectedVehicle.brandId}
      >
        {getBrand(selectedVehicle.brandId)?.manufactureYears.map((mfy) => (
          <MenuItem
            value={mfy.year}
            key={mfy.year}
            selected={selectedVehicle.mfy === mfy.year}
            onClick={() => {
              const newState = structuredClone(selectedVehicle);
              newState.mfy = mfy.year;
              newState.modelId = "";

              dispatch(setVehicle(newState));
            }}
          >
            {mfy.year}
          </MenuItem>
        ))}
      </Select>
      <Select
        id="can-bus-model"
        select
        helperText="Please select the vehicle model"
        value={selectedVehicle.modelId ?? ""}
        disabled={!isCanBusEnable || !selectedVehicle.mfy}
      >
        {getManufactureYears(
          getBrand(selectedVehicle.brandId),
          selectedVehicle.mfy
        )?.models.map((model) => (
          <MenuItem
            value={model.id}
            key={model.id}
            selected={selectedVehicle.modelId === model.id}
            onClick={() => {
              const newState = structuredClone(selectedVehicle);
              newState.modelId = model.id;
              newState.events = model.canEvents;
              dispatch(setVehicle(newState));
            }}
          >
            {model.name}
          </MenuItem>
        ))}
      </Select>
    </Root>
  );
}

function getBrand(id?: String) {
  return brands.find((v) => v.id === id);
}

function getManufactureYears(brand: VehicleBrand | undefined, year: number) {
  return brand?.manufactureYears.find((y) => y.year === year);
}

function getModel(manufactureYear: VehicleManufactureYear, modelId: string) {
  return manufactureYear.models.find((m) => m.id === modelId);
}
