import Button from '@mui/material/Button';
import { styled } from '@mui/material/styles';
import { useNavigate } from 'react-router-dom';
import {setInitialState} from '../../app/appSlice';
import {useAppDispatch} from '../../app/hooks';

const SystemButton = styled(Button)`
  padding: 15px 15px;
  min-width: 20vw;
  aspect-ratio: 1/1;
  margin: 4px;
` as typeof Button;

const VisuallyHiddenInput = styled('input')({
  clip: 'rect(0 0 0 0)',
  clipPath: 'inset(50%)',
  height: 1,
  overflow: 'hidden',
  position: 'absolute',
  bottom: 0,
  left: 0,
  whiteSpace: 'nowrap',
  width: 1,
});

export function System() {
  const navigate = useNavigate();
  const dispatch = useAppDispatch();

  return (
    <div>
      <SystemButton component="label" variant="outlined">Upload file
        <VisuallyHiddenInput type="file" accept='.json' onChange={async (event) => {
          const system = JSON.parse((await (event.target.files?.item(0)?.text())) ?? '{}');
          dispatch(setInitialState(system));

          navigate('configuration');
        }
        } />
      </SystemButton>
      <SystemButton variant="outlined"
        onClick={() => navigate('system')}>New System</SystemButton>
    </div>
  )
}
