import styled from "@emotion/styled";
import { Card, MenuItem, TextField } from "@mui/material";
import Box from "@mui/material/Box";
import {
  SequentialTrigger,
  System,
  Trigger,
  TriggeredKey,
  TriggeredOutput,
} from "../../../system/interfaces";
import * as utils from "../../../../utils";

const Root = styled(Box)`
  display: flex;
  flex: 1 1 auto;
  justify-content: space-around;
`;

const ListPanel = styled(Card)`
  display: flex;
  border: 1px solid rgba(0, 0, 0, 0.12);
  flex: 1 1 auto;
  flex-direction: column;
  margin: 4px;
  overflow: scroll;
  padding: 8px;
  div {
    margin: 4px;
  }
`;

const Row = styled(Box)`
  display: flex;
  flex: 1;

  div {
    flex: 1;
  }
`;

export function TriggeredInfo({
  system,
  onChange,
  selectedTrigger,
  selectedSequence = 0,
  selectedCondition = -1,
}: TriggeredInfoProperty) {
  const triggerInfo = system.can_events.at(selectedTrigger);

  const getOutputs = () => {
    let outputs: TriggeredOutput[] = [];

    if (triggerInfo!.type === 16) {
      const trigger = triggerInfo as SequentialTrigger;
      outputs =
        trigger.sequences?.at(selectedSequence)?.triggered_output_configs ?? [];
    } else {
      const trigger = triggerInfo as Trigger;
      if (selectedCondition > -1 &&  selectedCondition < trigger.conditions.length) {
        outputs =
          trigger.conditions.at(selectedCondition)!.triggered_output_configs;
      } else {
        outputs = trigger.triggered_output_configs;
      }
    }

    return outputs;
  };

  const getOutputState = (outputIndex: number) => {
    const outputs = getOutputs();

    return outputs.at(outputIndex)?.state || 0;
  };

  const getOutputPower = (outputIndex: number) => {
    const outputs = getOutputs();

    return (outputs.at(outputIndex)?.power ?? 0) * 5;
  };

  const updateOutput = (id: number, { state, power }: any) => {
    if(!power) {
      if(state === 2){
        power = 100;
      } else {
        power = 0;
      }
    }

    const output: TriggeredOutput = {
      id: id,
      state: state ?? getOutputState(id - 1),
      power: power !== undefined ? ~~(power / 5) : getOutputPower(id - 1) / 5,
      binary: [
        ...utils.parseBinary(id, 2),
        ...utils.parseBinary(~~(power / 5), 6),
      ],
    };

    if (triggerInfo?.type === 16) {
      const trigger = triggerInfo as SequentialTrigger;
      trigger.sequences.at(selectedSequence)!.triggered_output_configs[id - 1] =
        { ...output };
    } else {
      if (selectedCondition > -1 && selectedCondition < (triggerInfo as Trigger).conditions.length) {
        const trigger = triggerInfo as Trigger;
        trigger.conditions.at(selectedCondition)!.triggered_output_configs[
          id - 1
        ] = output;
      } else {
        const trigger = triggerInfo as Trigger;
        trigger.triggered_output_configs[id - 1] = output;
      }
    }

    return { ...system };
  };

  const getKeys = () => {
    let keys: TriggeredKey[] = [];

    if (triggerInfo!.type === 16) {
      const trigger = triggerInfo as SequentialTrigger;
      keys =
        trigger.sequences?.at(selectedSequence)?.triggered_key_configs ?? [];
    } else {
      const trigger = triggerInfo as Trigger;
      if (selectedCondition > -1 &&  selectedCondition < trigger.conditions.length) {
        keys = trigger.conditions.at(selectedCondition)!.triggered_key_configs;
      } else {
        keys = trigger.triggered_key_configs;
      }
    }

    return keys;
  };

  const getKeyId = (index: number) => {
    return getKeys()?.at(index)?.id ?? 0;
  };

  const getKeyState = (index: number) => {
    const keys = getKeys();

    const keyId = getKeyId(index);

    const key = keys.filter((k) => k.id === keyId);
    return key[0]?.state ?? 0;
  };

  const getKey = (index: number) => {
    const keys = getKeys();

    const keyId = getKeyId(index);

    const key = keys.filter((k) => k.id === keyId);

    return key[0];
  };

  const getIndexByKeyId = (id: number) => {
    const keys = getKeys();

    return keys.findIndex((k) => k.id === id) ?? -1;
  };

  const getKeySequence = (index: number) => {
    const keys = getKeys();

    const keyId = getKeyId(index);

    const key = keys.filter((k) => k.id === keyId);
    return key[0]?.sequence ?? 0;
  };

  const updateKey = (
    index: number,
    keyId: number,
    { state, sequence }: any
  ) => {
    const currentKey = getKey(index);

    const newKey: TriggeredKey = {
      id: keyId,
      state: state !== undefined ? state : currentKey.state ?? 0,
      sequence: sequence !== undefined ? sequence : currentKey.state ?? 0,
      binary: [],
    };

    newKey.binary = [...utils.parseBinary(newKey.state, 2)];

    if (triggerInfo!.type === 16) {
      const trigger = triggerInfo as SequentialTrigger;
      trigger.sequences[selectedSequence].triggered_key_configs[index] = newKey;
    } else {
      const trigger = triggerInfo as Trigger;
      if (selectedCondition > -1 &&  selectedCondition < trigger.conditions.length) {
        trigger.conditions.at(selectedCondition)!.triggered_key_configs[index] =
          newKey;
      } else {
        trigger.triggered_key_configs[index] = newKey;
      }
    }

    return { ...system };
  };

  return (
    <Root>
      <ListPanel elevation={0}>
        {[...Array(21).keys()].map((i) => (
          <Row>
            <TextField
              label={`Output ${i + 1}`}
              select
              value={getOutputState(i)}
            >
              <MenuItem
                value={0}
                onClick={() => {
                  onChange(
                    updateOutput(i + 1, {
                      state: 0,
                    })
                  );
                }}
              >
                Not used
              </MenuItem>
              <MenuItem
                value={1}
                onClick={() => {
                  onChange(
                    updateOutput(i + 1, {
                      state: 1,
                    })
                  );
                }}
              >
                Off
              </MenuItem>
              <MenuItem
                value={2}
                onClick={() => {
                  onChange(
                    updateOutput(i + 1, {
                      state: 2,
                    })
                  );
                }}
              >
                On
              </MenuItem>
            </TextField>
            <TextField label="Power" select value={getOutputPower(i)} disabled={getOutputState(i) !== 2}>
              {[...Array(101).keys()].map(
                (p) =>
                  p % 5 === 0 && (
                    <MenuItem
                      value={p}
                      onClick={() => {
                        onChange(
                          updateOutput(i + 1, {
                            power: p,
                          })
                        );
                      }}
                    >
                      {p}
                    </MenuItem>
                  )
              )}
            </TextField>
          </Row>
        ))}
      </ListPanel>
      <ListPanel elevation={0}>
        {[...Array(4).keys()].map((i) => (
          <Row>
            <TextField select label="Key" value={getKeyId(i)}>
              {[
                <MenuItem
                  value={0}
                  onClick={() =>
                    onChange(
                      updateKey(i, 0, {
                        state: 0,
                        sequence: 0,
                      })
                    )
                  }
                >
                  Select a key
                </MenuItem>,
                ...[...Array(system.number_of_keys).keys()].map((k) => {
                  return (
                    <MenuItem
                      disabled={
                        getIndexByKeyId(k + 1) > -1
                      }
                      value={k + 1}
                      onClick={() =>
                        onChange(
                          updateKey(i, k + 1, {
                            state: 0,
                            sequence: 0,
                          })
                        )
                      }
                    >
                      Key {k + 1}
                    </MenuItem>
                  );
                }),
                ...[...Array(16).keys()].map((v) => {
                  return (
                    <MenuItem
                      disabled={
                        getIndexByKeyId(v + 33) > -1
                      }
                      value={v + 33}
                      onClick={() =>
                        onChange(
                          updateKey(i, v + 33, {
                            state: 0,
                            sequence: 0,
                          })
                        )
                      }
                    >
                      Virtual Key {v + 1}
                    </MenuItem>
                  );
                }),
              ]}
            </TextField>
            <TextField label="Key State" select value={getKeyState(i)}>
              <MenuItem
                value={0}
                onClick={() => {
                  onChange(
                    updateKey(i, getKeyId(i), {
                      state: 0,
                    })
                  );
                }}
              >
                Not used
              </MenuItem>
              <MenuItem
                value={1}
                onClick={() => {
                  onChange(
                    updateKey(i, getKeyId(i), {
                      state: 1,
                    })
                  );
                }}
              >
                Off
              </MenuItem>
              <MenuItem
                value={2}
                onClick={() => {
                  onChange(
                    updateKey(i, getKeyId(i), {
                      state: 2,
                    })
                  );
                }}
              >
                On
              </MenuItem>
            </TextField>
            {system.keys.at((getKey(i)?.id ?? 0) - 1)?.type === 16 && (
              <TextField
                disabled={getKeyId(i) === selectedTrigger}
                label="Sequence"
                select
                value={getKeySequence(i)}
              >
                {[
                  <MenuItem
                    value={0}
                    onClick={() => {
                      onChange(
                        updateKey(i, getKeyId(i), {
                          sequence: 0,
                        })
                      );
                    }}
                  >
                    Select a sequence
                  </MenuItem>,
                  ...[
                    ...Array(
                      Math.min(
                        3,
                        (
                          system.keys.at(
                            (getKey(i)?.id ?? 0) - 1
                          ) as SequentialTrigger
                        ).sequences?.length || 0
                      )
                    ).keys(),
                  ].map((s) => (
                    <MenuItem
                      value={s + 1}
                      onClick={() => {
                        onChange(
                          updateKey(i, getKeyId(i), {
                            sequence: s + 1,
                          })
                        );
                      }}
                    >
                      {s + 1}
                    </MenuItem>
                  )),
                ]}
              </TextField>
            )}
          </Row>
        ))}
      </ListPanel>
    </Root>
  );
}

interface TriggeredInfoProperty {
  system: System;
  onChange(value: System): void;
  selectedTrigger: number;
  selectedSequence?: number;
  selectedCondition?: number;
}
