import styled from "@emotion/styled";
import {
  Button,
} from "@mui/material";
import Box from "@mui/material/Box";
import { useState } from "react";
import { SequentialTrigger, System, Trigger } from "../../../system/interfaces";
import { TriggerInfo } from "./TriggerInfo";
import { TriggeredInfo } from "./TriggeredInfo";

const Root = styled(Box)`
  display: flex;
  flex: 1 1 auto;
  flex-direction: row;
  margin: 4px;
  padding: 16px;
`;

const HornRingListBox = styled(Box)`
  bottom: 0;
  min-width: 200px;
  display: flex;
  flex-direction: column;
`;

const HornRingList = styled(Box)`
  display: flex;
  flex: 1;
  flex-direction: column;
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-radius: 4px;
  overflow: scroll;
  padding: 8px;
  margin: 8px;
`;

export function HornRingsConfiguration({
  system,
  onChange,
}: HornRingsConfigurationProperty) {
  const [selectedHornRing, selectHornRing] = useState(1);
  const [selectedSequence, selectSequence] = useState(0);
  const [selectedCondition, selectCondition] = useState(-1);

  return (
    <Root>
      <HornRingsPanel
        onHornRingSelect={selectHornRing}
        selectedHornRing={selectedHornRing}
        items={system.horn_ring}
      />
      <TriggerInfo
        numberOfKey={system.number_of_keys}
        numberOfEvent={system.number_of_can_events}
        trigger={system.horn_ring.at(selectedHornRing - 1)}
        selectedSequence={selectedSequence}
        onSequenceSelect={selectSequence}
        selectedCondition={selectedCondition}
        onConditionSelect={selectCondition}
        onChange={(t) => {
          system.horn_ring[selectedHornRing - 1] = t;
          onChange({ ...system });
        }}
      />
      <Box
        sx={{
          width: "300px",
          // border: '1px solid rgba(0, 0, 0, 0.12)',
          borderRadius: "4px",
          margin: "0 16px",
          flex: "4",
          display: "flex",
        }}
      >
        <TriggeredInfo
          system={system}
          selectedTrigger={selectedHornRing - 1}
          selectedSequence={selectedSequence}
          selectedCondition={selectedCondition}
          onChange={(system) => onChange({ ...system })}
        />
      </Box>
    </Root>
  );
}

function HornRingsPanel({
  onHornRingSelect,
  selectedHornRing,
  items,
}: HornRingListProperty) {
  return (
    <HornRingListBox
      sx={{
        display: 'none !important',
      }}
    >
      <HornRingList>
        {items.map((i) => (
          <HornRingButton
            index={i.id}
            name={`${(i as Trigger).name ?? "Sequential"}`}
            onClick={() => onHornRingSelect(i.id)}
            isSelected={selectedHornRing === i.id}
          />
        ))}
      </HornRingList>
    </HornRingListBox>
  );
}

function HornRingButton({
  index,
  name,
  onClick,
  isSelected = false,
}: HornRingButtonProperty) {
  return (
    <Button
      key={index}
      variant="outlined"
      sx={{
        display: "flex",
        flexDirection: "column !important",
        alignItems: "stretch",
        margin: "4px",
        backgroundColor: isSelected ? "rgba(25, 118, 210, 0.5)" : "",
      }}
      onClick={(_) => !!onClick && onClick()}
    >
      <Box
        sx={{
          display: "flex",
          justifyContent: "space-around",
        }}
      >
        {`${name} (${index})`}
      </Box>
    </Button>
  );
}

interface HornRingsConfigurationProperty {
  system: System;
  onChange(value: System): void;
}

interface HornRingListProperty {
  onHornRingSelect: Function;
  selectedHornRing?: number;
  items: Trigger[] | SequentialTrigger[];
}

interface HornRingButtonProperty {
  index: number;
  name: string;
  onClick?: Function;
  isSelected?: boolean;
}
