import styled from "@emotion/styled";
import {
  Button,
} from "@mui/material";
import Box from "@mui/material/Box";
import { useState } from "react";
import { SequentialTrigger, System, Trigger } from "../../../system/interfaces";
import { TriggerInfo } from "./TriggerInfo";
import { TriggeredInfo } from "./TriggeredInfo";

const Root = styled(Box)`
  display: flex;
  flex: 1 1 auto;
  flex-direction: row;
  margin: 4px;
  padding: 16px;
`;

const InputListBox = styled(Box)`
  bottom: 0;
  min-width: 200px;
  display: flex;
  flex-direction: column;
`;

const InputList = styled(Box)`
  display: flex;
  flex: 1;
  flex-direction: column;
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-radius: 4px;
  overflow: scroll;
  padding: 8px;
  margin: 8px;
`;

export function InputsConfiguration({
  system,
  onChange,
}: InputsConfigurationProperty) {
  const [selectedInput, selectInput] = useState(1);
  const [selectedSequence, selectSequence] = useState(0);
  const [selectedCondition, selectCondition] = useState(-1);

  return (
    <Root>
      <InputsPanel
        numberOfInputs={system.number_of_inputs}
        onInputSelect={selectInput}
        selectedInput={selectedInput}
        items={system.inputs}
      />
      <TriggerInfo
        trigger={system.inputs.at(selectedInput - 1)}
        numberOfKey={system.number_of_keys}
        numberOfEvent={system.number_of_can_events}
        selectedSequence={selectedSequence}
        onSequenceSelect={selectSequence}
        selectedCondition={selectedCondition}
        onConditionSelect={selectCondition}
        onChange={(t) => {
          system.inputs[selectedInput - 1] = t;
          onChange({ ...system });
        }}
      />
      <Box
        sx={{
          width: "300px",
          // border: '1px solid rgba(0, 0, 0, 0.12)',
          borderRadius: "4px",
          margin: "0 16px",
          flex: "4",
          display: "flex",
        }}
      >
        <TriggeredInfo
          system={system}
          selectedTrigger={selectedInput - 1}
          selectedSequence={selectedSequence}
          selectedCondition={selectedCondition}
          onChange={(system) => onChange({ ...system })}
        />
      </Box>
    </Root>
  );
}

function InputsPanel({
  numberOfInputs,
  onInputSelect,
  selectedInput,
  items,
}: InputListProperty) {
  return (
    <InputListBox>
      <InputList>
        {[...Array(numberOfInputs).keys()].map((i) => (
          <InputButton
            index={items[i].id}
            name={`${(items[i] as Trigger).name ?? "Sequential"}`}
            onClick={() => onInputSelect(items[i].id)}
            isSelected={selectedInput === items[i].id}
          />
        ))}
      </InputList>
    </InputListBox>
  );
}

function InputButton({
  index,
  name,
  onClick,
  isSelected = false,
}: InputButtonProperty) {
  return (
    <Button
      variant="outlined"
      sx={{
        display: "flex",
        flexDirection: "column !important",
        alignItems: "stretch",
        margin: "4px",
        backgroundColor: isSelected ? "rgba(25, 118, 210, 0.5)" : "",
      }}
      onClick={(_) => !!onClick && onClick()}
    >
      <Box
        sx={{
          display: "flex",
          justifyContent: "space-around",
        }}
      >
        {`${name} (${index})`}
      </Box>
    </Button>
  );
}

interface InputsConfigurationProperty {
  system: System;
  onChange(value: System): void;
}

interface InputListProperty {
  numberOfInputs: number;
  onInputSelect: Function;
  selectedInput?: number;
  items: Trigger[] | SequentialTrigger[];
}

interface InputButtonProperty {
  index: number;
  name: string;
  onClick?: Function;
  isSelected?: boolean;
}
