/* eslint no-extend-native:0 */

import * as _ from "lodash";
import FileSaver from "file-saver";

Number.prototype.parseBinary = function ({ maxLength = 0 }) {
  return this.valueOf().toString(2).padStart(maxLength, "0").split("");
};

String.prototype.toInt = function () {
  return parseInt(this);
};

String.prototype.toDecimal = function () {
  return parseFloat(this);
};

String.prototype.toBinary = function () {
  return this.charCodeAt(0).parseBinary({ maxLength: 8 });
};

export const KEY_TYPES = {
  sequential: 16,
  delay: 8,
  toggle_long: 4,
  toggle_short: 2,
  momentary: 1,
  toggle: 6,
};

export const hexToRgb = (hex) =>
  hex
    .replace(
      /^#?([a-f\d])([a-f\d])([a-f\d])$/i,
      (m, r, g, b) => "#" + r + r + g + g + b + b
    )
    .substring(1)
    .match(/.{2}/g)
    .map((x) => parseInt(x, 16));

export const rgbToHex = (r, g, b) =>
  "#" + [r, g, b].map((x) => x.toString(16).padStart(2, "0")).join("");

export function generateBinaryMatrix(json) {
  const matrix = [];

  if (!json) {
    return matrix;
  }

  // Add 16 reserved bytes
  matrix.push(..._.fill(Array(16), _.fill(Array(8), 0)));

  matrix.push(json.time_after_ign_off.parseBinary({ maxLength: 8 }));
  matrix.push(json.number_of_can_events.parseBinary({ maxLength: 8 }));
  matrix.push([
    json.keyboard_type,
    ...json.number_of_keys.parseBinary({ maxLength: 7 }),
  ]);
  matrix.push([
    json.has_photocell,
    ...json.photocell_sensitivity.parseBinary({ maxLength: 7 }),
  ]);
  matrix.push(json.beeper_timer.parseBinary({ maxLength: 8 }));
  matrix.push(json.toggle_long_time.parseBinary({ maxLength: 8 }));
  matrix.push(json.toggle_short_time.parseBinary({ maxLength: 8 }));
  matrix.push([
    ...Math.max(0, json.number_of_keyboard_led - 1).parseBinary({ maxLength: 3 }),
    ...json.first_led_number.parseBinary({ maxLength: 5 }),
  ]);

  matrix.push(...getKeysTypeMatrix(json.keys));
  matrix.push(...getInputsTypeMatrix(json.inputs));
  matrix.push([
    json.analog_input_one_state,
    ...json.analog_input_one_voltage.parseBinary({ maxLength: 7 }),
  ]);
  matrix.push(getAnalogInput2TypeMatrix(json.analog_inputs));
  matrix.push(getAnalogInput2TypeMatrix(json.horn_ring));
  matrix.push(...getVirtualKeysTypeMatrix(json.virtual_keys));
  matrix.push(...getCanEvnetTypeMatrix(json.can_events));

  // rotary_switch_steps,
  // rotary_key_first_position,
  matrix.push([
    ...(json.rotary_switch_steps - 2 > 0
      ? json.rotary_switch_steps - 2
      : 0
    ).parseBinary({ maxLength: 3 }),
    ...json.rotary_key_first_position.parseBinary({ maxLength: 5 }),
  ]);

  // slide_switch_steps,
  // slide_key_first_position,
  matrix.push([
    ...(json.slide_switch_steps - 2 > 0
      ? json.slide_switch_steps - 2
      : 0
    ).parseBinary({ maxLength: 3 }),
    ...json.slide_key_first_position.parseBinary({ maxLength: 5 }),
  ]);

  // number_of_inputs,
  matrix.push(json.number_of_inputs.parseBinary({ maxLength: 8 }));

  // temperature_operator,
  // temperature_value,
  matrix.push([
    json.temperature_operator.toString(),
    json.temperature_value >= 0 ? "0" : "1",
    ...["0", "0", "0", "0"],
    ...Math.abs(json.temperature_value * 10)
      .parseBinary({ maxLength: 10 })
      .slice(0, 2),
  ]);
  matrix.push([
    ...Math.abs(json.temperature_value * 10)
      .parseBinary({ maxLength: 10 })
      .slice(2),
  ]);

  // g_force,
  matrix.push((json.g_force * 10).parseBinary({ maxLength: 8 }));

  // Add 80 reserved bytes
  matrix.push(..._.fill(Array(73), _.fill(Array(8), 0)));

  matrix.push(...getKeysConfigsMatrix({ keys: json.keys }));

  matrix.push(...getKeysConfigsMatrix({ keys: json.inputs, maxRowLength: 14 }));

  matrix.push(
    ...getKeysConfigsMatrix({
      keys: json.analog_inputs,
      maxRowLength: 2,
      isAnalogInput: true,
    })
  );

  matrix.push(
    ...getKeysConfigsMatrix({ keys: json.horn_ring, maxRowLength: 1 })
  );

  matrix.push(
    ...getKeysConfigsMatrix({ keys: json.virtual_keys, maxRowLength: 16 })
  );

  matrix.push(
    ...getKeysConfigsMatrix({ keys: json.can_events, maxRowLength: 32 })
  );

  return matrix;
}

export async function generateBinaryFile(json) {
  const matrix = generateBinaryMatrix(json);

  let buffer = new ArrayBuffer(matrix.length);

  let view = new Uint8Array(buffer);

  matrix.forEach(function (value, index) {
    view[index] = parseInt(value.join(""), 2);
  });

  const blob = new Blob([buffer], { type: "application/octet-stream" });

  const suggestedName = `keyboard-config-${new Date().getTime()}.bin`;

  if( window.showSaveFilePicker ) {
    const options = {
      suggestedName, 
      types: [
        {
          description: "BIN file",
          accept: { "text/plain": [".bin"] },
        },
      ],
    };
    const handle = await window.showSaveFilePicker(options);
    const writable = await handle.createWritable();
    await writable.write( blob );
    writable.close();
  } else {
    FileSaver.saveAs(blob, `keyboard-config-${new Date().getTime()}.bin`);
  }
}

export async function generateJsonFile(json) {
  var blob = new Blob([JSON.stringify(json)], {
    type: "text/plain;charset=utf-8",
  });

  const suggestedName = `keyboard-config-${new Date().getTime()}.json`;

  if( window.showSaveFilePicker ) {
    const options = {
      suggestedName, 
      types: [
        {
          description: "JSON file",
          accept: { "text/plain": [".json"] },
        },
      ],
    };
    const handle = await window.showSaveFilePicker(options);
    const writable = await handle.createWritable();
    await writable.write( blob );
    writable.close();
  } else {
    FileSaver.saveAs(blob, suggestedName);
  }
}

function getKeysTypeMatrix(keys) {
  const matrix = [..._.fill(Array(32), _.fill(Array(8), "0"))];

  keys.forEach(function (k) {
    if (k.type === KEY_TYPES.sequential) {
      matrix[k.id - 1] = [
        ...k.type.parseBinary({ maxLength: 5 }),
        ...(k.sequences.length > 0? k.sequences.length - 1 : 0).parseBinary({ maxLength: 3 }),
      ];
    } else if(k.type === KEY_TYPES.toggle) {
      matrix[k.id - 1] = [
        ...(6).parseBinary({ maxLength: 5 }),
        ..._.fill(Array(3), "0"),
      ];
    } else {
      matrix[k.id - 1] = [
        ...k.type.parseBinary({ maxLength: 5 }),
        ..._.fill(Array(3), "0"),
      ];
    }
  });

  return matrix;
}

function getInputsTypeMatrix(inputs) {
  const matrix = [..._.fill(Array(14), _.fill(Array(8), "0"))];

  inputs.forEach(function (input) {
    if (input.type === KEY_TYPES.sequential) {
      matrix[input.id - 1] = [
        ...input.type.parseBinary({ maxLength: 5 }),
        ...(input.sequences.length > 0? input.sequences.length - 1 : 0).parseBinary({ maxLength: 3 }),
      ];
    } else if(input.type === KEY_TYPES.toggle) {
      matrix[input.id - 1] = [
        ...(6).parseBinary({ maxLength: 5 }),
        ...(input.sequences.length > 0? input.sequences.length - 1 : 0).parseBinary({ maxLength: 3 }),
      ];
    } else {
      matrix[input.id - 1] = [
        ...input.type.parseBinary({ maxLength: 5 }),
        ..._.fill(Array(3), "0"),
      ];
    }
  });

  return matrix;
}

function getAnalogInput2TypeMatrix(analogs) {
  let matrix = _.fill(Array(8), "0");

  if (!analogs || !analogs.length) {
    return matrix;
  }

  matrix = [
    ...analogs[0].type.parseBinary({ maxLength: 5 }),
    ...(analogs[0].type === KEY_TYPES.sequential || analogs[0].type === KEY_TYPES.toggle
      ? (analogs[0].sequences.length > 0 ? analogs[0].sequences.length -1 : 0).parseBinary({ maxLength: 3 })
      : _.fill(Array(3), "0")),
  ];

  return matrix;
}

function getVirtualKeysTypeMatrix(keys) {
  const matrix = [..._.fill(Array(16), _.fill(Array(8), "0"))];

  keys.forEach(function (k) {
    if ([KEY_TYPES.delay, KEY_TYPES.momentary].indexOf(k.type) > -1) {
      matrix[k.id - 1] = [
        ...k.type.parseBinary({ maxLength: 5 }),
        ..._.fill(Array(3), "0"),
      ];
    }
  });

  return matrix;
}

function getCanEvnetTypeMatrix(events) {
  return getKeysTypeMatrix(events);
}

function getKeysConfigsMatrix({
  keys = [],
  maxRowLength = 32,
  isAnalogInput = false,
}) {
  const matrix = {};

  keys.forEach(function (k) {
    const key_matrix = [];

    if (isAnalogInput) {
      const voltage = k.analog_value;
      const scale =
        `${voltage}`.split(".").length > 1
          ? `${voltage}`.split(".").slice(-1)[0].length
          : 0;
      const voltage_bits = (+(voltage * Math.pow(10, scale)).toFixed(
        0
      )).parseBinary({ maxLength: 12 });
      key_matrix.push([
        k.analog_value_operator.toString(),
        k.analog_value_show_on_display.toString(),
        ...(+scale).parseBinary({ maxLength: 2 }),
        ...voltage_bits.slice(0, 4),
      ]);
      key_matrix.push(voltage_bits.slice(4, 12));
    } else {
      // Analog value which is only filled for Analog inputs.
      key_matrix.push(..._.fill(Array(2), _.fill(Array(8), "0")));
    }

    // Conditions config which will be filled later.
    const conditions_matrix = [..._.fill(Array(4), _.fill(Array(8), "0"))];
    k.conditions?.forEach(function (c, index) {
      conditions_matrix[index] = c.key_id.parseBinary({ maxLength: 8 });
    });

    key_matrix.push(...conditions_matrix);

    // Add key name
    // If the key type is sequential then the name will comes from it's first sequence.
    if ((k.type === KEY_TYPES.sequential || k.type === KEY_TYPES.toggle) && k.sequences.length) {
      k.sequences.forEach(function (s) {
        s.off_bg = k.off_bg;
        key_matrix.push(...getKeyConfigsMatrix(s));
      });
    } else if (k.type !== KEY_TYPES.sequential && k.type !== KEY_TYPES.toggle) {
      key_matrix.push(...getKeyConfigsMatrix(k));

      if (!!k.conditions.length) {
        k.conditions.forEach(function (c) {
          key_matrix.push(...getKeyConfigsMatrix(k, c));
        });
      }
    }

    if (key_matrix.length < 600) {
      const diff = 600 - key_matrix.length;
      key_matrix.push(..._.fill(Array(diff), _.fill(Array(8), "0")));
    }

    matrix[k.id] = key_matrix;
  });

  const final_matrix = [];
  for (let i = 1; i <= maxRowLength; i++) {
    if (matrix[i]) {
      final_matrix.push(...matrix[i]);
    } else {
      final_matrix.push(..._.fill(Array(600), _.fill(Array(8), "0")));
    }
  }

  return final_matrix;
}

function getKeyConfigsMatrix(key, condition) {
  const matrix = [];

  key.name
    ?.slice(0, 16)
    .split("")
    .forEach((c) => matrix.push(c.toBinary()));

  if (key.name.length < 16) {
    matrix.push(..._.fill(Array(16 - key.name.length), _.fill(Array(8), "0")));
  }

  // Priority
  matrix.push(key.priority.parseBinary({ maxLength: 8 }));

  // DIMM/CRUSE
  matrix.push([
    ...key.dimm_cruse.parseBinary({ maxLength: 1 }),
    ...key.dimm_cruse_value.parseBinary({ maxLength: 7 }),
  ]);

  // IGN
  matrix.push([
    ...key.ignition.parseBinary({ maxLength: 2 }),
    ..._.fill(Array(6), "0"),
  ]);

  // Pattern
  matrix.push([
    key.pattern_sync,
    ...key.pattern_id.parseBinary({ maxLength: 7 }),
  ]);

  // Delay
  matrix.push([...(key.delay_duration || 0).parseBinary({ maxLength: 8 })]);

  // Smart lightbar
  matrix.push([
    key.smart_lightbar_sync,
    ...key.smart_lightbar_id.parseBinary({ maxLength: 7 }),
  ]);

  // Smart side flash
  matrix.push([
    key.smart_side_flash_sync,
    ...key.smart_side_flash_id.parseBinary({ maxLength: 7 }),
  ]);

  // Smart LED message
  matrix.push([
    key.smart_led_message_sync,
    ...key.smart_led_message_id.parseBinary({ maxLength: 7 }),
  ]);

  // Tone
  matrix.push([key.tone_sync, ...key.tone_id.parseBinary({ maxLength: 7 })]);

  // Off mode BG
  matrix.push(
    ...(key.off_bg || [0, 0, 0]).map((c) =>
      c.parseBinary({ maxLength: 8 })
    )
  );

  // On mode BG
  matrix.push(...key.on_mode_bg.map((c) => c.parseBinary({ maxLength: 8 })));

  // Triggered outputs
  const triggered_outputs = _.fill(Array(38), _.fill(Array(8), "0"));
  if (!!condition) {
    condition.triggered_output_configs.forEach(function (to) {
      const id = to.id > 21 ? to.id : to.id - 1;

      triggered_outputs[id] = [
        ...to.state.parseBinary({ maxLength: 2 }),
        ...(to.power || 0).parseBinary({ maxLength: 6 }),
      ];
    });
  } else {
    key.triggered_output_configs.forEach(function (to) {
      if(!to) {
        return;
      }
      
      const id = to.id > 21 ? to.id : to.id - 1;

      triggered_outputs[id] = [
        ...to.state.parseBinary({ maxLength: 2 }),
        ...(to.power || 0).parseBinary({ maxLength: 6 }),
      ];
    });
  }

  // Horn ring loop
  triggered_outputs[21] = [
    ...key.horn_ring_loop.parseBinary({ maxLength: 2 }),
    ..._.fill(Array(6), "0"),
  ];

  matrix.push(...triggered_outputs);

  // Triggered Keys
  const keys_states = [];
  const keys_id = [];

  if (!!condition) {
    condition.triggered_key_configs.slice(0, 4).forEach(function (tk) {
      keys_states.push(tk.state.parseBinary({ maxLength: 2 }));
      keys_id.push([
        ...(tk.sequence || 0).parseBinary({ maxLength: 2 }),
        ...tk.id.parseBinary({ maxLength: 6 }),
      ]);
    });
  } else {
    key.triggered_key_configs.slice(0, 4).forEach(function (tk) {
      keys_states.push(tk.state.parseBinary({ maxLength: 2 }));
      keys_id.push([
        ...(tk.sequence || 0).parseBinary({ maxLength: 2 }),
        ...tk.id.parseBinary({ maxLength: 6 }),
      ]);
    });
  }

  keys_states.push(..._.fill(Array(4 - keys_states.length), ["0", "0"]));

  matrix.push(_.flatten(keys_states));
  matrix.push(...keys_id);
  matrix.push(..._.fill(Array(4 - keys_id.length), _.fill(Array(8), "0")));

  return matrix;
}
