import styled from "@emotion/styled";
import {
  Button,
  ToggleButton,
  ToggleButtonGroup,
  Tooltip,
  Typography,
} from "@mui/material";
import Box from "@mui/material/Box";
import { useState } from "react";
import { SequentialTrigger, System, Trigger } from "../../../system/interfaces";
import { TriggerInfo } from "./TriggerInfo";
import { TriggeredInfo } from "./TriggeredInfo";

const Root = styled(Box)`
  display: flex;
  flex: 1 1 auto;
  flex-direction: row;
  margin: 4px;
  padding: 16px;
`;

const KeyListBox = styled(Box)`
  bottom: 0;
  min-width: 200px;
  display: flex;
  flex-direction: column;
`;

const KeyList = styled(Box)`
  display: flex;
  flex: 1;
  flex-direction: column;
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-radius: 4px;
  overflow: scroll;
  padding: 8px;
  margin: 8px;
`;

export function VirtualKeysConfiguration({
  system,
  onChange,
}: VirtualKeysConfigurationProperty) {
  const [selectedKey, selectKey] = useState(0);
  const [selectedSequence, selectSequence] = useState(0);
  const [selectedCondition, selectCondition] = useState(-1);

  return (
    <Root>
      <KeysPanel
        onKeySelect={selectKey}
        selectedKey={selectedKey}
        items={system.virtual_keys}
      />
      <TriggerInfo
        trigger={system.virtual_keys.at(selectedKey - 1)}
        numberOfKey={system.number_of_keys}
        numberOfEvent={system.number_of_can_events}
        selectedSequence={selectedSequence}
        onSequenceSelect={selectSequence}
        selectedCondition={selectedCondition}
        onConditionSelect={selectCondition}
        onChange={(t) => {
          system.virtual_keys[selectedKey - 1] = t;
          onChange({ ...system });
        }}
      />
      <Box
        sx={{
          width: "300px",
          // border: '1px solid rgba(0, 0, 0, 0.12)',
          borderRadius: "4px",
          margin: "0 16px",
          flex: "4",
          display: "flex",
        }}
      >
        <TriggeredInfo
          system={system}
          selectedTrigger={selectedKey - 1}
          selectedSequence={selectedSequence}
          selectedCondition={selectedCondition}
          onChange={(system) => onChange({ ...system })}
        />
      </Box>
    </Root>
  );
}

function KeysPanel({
  onKeySelect,
  selectedKey,
  items,
}: KeyListProperty) {

  return (
    <KeyListBox>
      <KeyList>
        {items.map((i) => (
          <KeyButton
            index={i.id}
            name={`${(i as Trigger).name ?? "Sequential"}`}
            onClick={() => onKeySelect(i.id)}
            isSelected={selectedKey === i.id}
          />
        ))}
      </KeyList>
    </KeyListBox>
  );
}

function KeyButton({
  index,
  name,
  onClick,
  isSelected = false,
}: KeyButtonProperty) {
  return (
    <Button
      variant="outlined"
      sx={{
        display: "flex",
        flexDirection: "column !important",
        alignItems: "stretch",
        margin: "4px",
        backgroundColor: isSelected ? "rgba(25, 118, 210, 0.5)" : "",
      }}
      onClick={(_) => !!onClick && onClick()}
    >
      <Box
        sx={{
          display: "flex",
          justifyContent: "space-around",
        }}
      >
        {`${name} (${index})`}
      </Box>
    </Button>
  );
}

interface VirtualKeysConfigurationProperty {
  system: System;
  onChange(value: System): void;
}

interface KeyListProperty {
  onKeySelect: Function;
  selectedKey?: number;
  items: Trigger[] | SequentialTrigger[];
}

interface KeyButtonProperty {
  index: number;
  name: string;
  onClick?: Function;
  isSelected?: boolean;
}
