const Field = styled(MTextField)`
  margin: 16px;
`;

const MainRoot = styled(Root)`
  display: block;
`;

const NumberField = ({ label, value, min, max, onChange, sx, step = 1, disabled = false, }: any) => (
  <Field
    type="number"
    sx={sx}
    label={label}
    value={value}
    InputProps={{ inputProps: { min: min, max: max, step: step } }}
    disabled = {disabled ?? false}
    onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
      onChange(+event.target.value);
    }}
  />
);

const Select = ({ label, values, selectedValue, onChange, sx }: any) => (
  <Field sx={sx} select label={label} value={selectedValue ?? ""}>
    {values.map((v: any, i: any) => (
      <MenuItem
        value={v}
        key={v + i}
        selected={i === selectedValue}
        onClick={() => onChange(i)}
      >
        {v}
      </MenuItem>
    ))}
  </Field>
);

