export const AllConfigs = memo(function () {
  const configs = useAppSelector(selectSystem);
  const dispatch = useAppDispatch();
  const [openedPanel, setOpenedPanel] = useState(-1);
  const selectedPanel = useMemo(() => openedPanel, [openedPanel]);

    const shouldDisabled = (key: keyof System): boolean => {
      switch(key) {
      case 'photocell_sensitivity':
        return configs['has_photocell'] === 0;
      case 'analog_input_one_voltage':
        return configs['analog_input_one_state'] === 0;
      default:
        return false;
     }
    }

  return (
    <MainRoot
      sx={{
        display: "block",
      }}
    >
      {Object.keys(fields).map((k: string, index: number) => (
        <Accordion
          key={index}
          expanded={selectedPanel===index}
          onChange={() => setOpenedPanel(selectedPanel !== index ? index : -1)}
          sx={{
            width: "100%",
          }}
        >
          <AccordionSummary expandIcon={<ExpandMore />}>
            <Typography>{k}</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Root
              sx={{
                height: "unset !important",
                justifyContent: "space-between !important",
                "> div": {
                  width: "40%",
                },
              }}
            >
              {Object.values(fields[k as keyof typeof fields]).map(
                (value: any) =>
                  value.component((e: any) => {
                    const newConfigs = structuredClone(configs);
                    newConfigs[value.key as keyof System] = e;
                    dispatch(setSystem(newConfigs));
                  }, configs[value.key as keyof System], shouldDisabled(value.key))
              )}
            </Root>
          </AccordionDetails>
        </Accordion>
      ))}
    </MainRoot>
  );
}, (p, n) => false);
