import { Box } from "@mui/system";
import { ReactComponent as IqProFalcon } from "../assets/touchpads/iq_pro_falcon.svg";
import { ReactComponent as IqProMaster16 } from "../assets/touchpads/iq_pro_master_16.svg";
import { ReactComponent as IqProMaster21 } from "../assets/touchpads/iq_pro_master_21.svg";
import { ReactComponent as IqProRotarySlide14 } from "../assets/touchpads/iq_pro_rotary_slide_14.svg";
import { ReactComponent as IqProSlide17 } from "../assets/touchpads/iq_pro_slide_17.svg";
import { ReactComponent as IqProRotaryMaster15 } from "../assets/touchpads/iq_pro_rotary_master_15.svg";
import { ReactComponent as IqProSlide20 } from "../assets/touchpads/iq_pro_slide_20.svg";

const Card = styled(MCard)`
  flex-direction: row;
  content-justify: space-between;
  margin: 16px;
`;

const InfoRoot = styled.div`
  display: flex;
  flex-direction: row;
  flex: 4;
  justify-content: space-between;
  flex-wrap: wrap;
  div {
    width: 30%;
    padding: 4px;
  }
`;

const Row = styled(Box)`
  display: flex;
  justify-content: space-around;
`;

const Image = styled(Box)`
  display: flex;
  flex-direction: column;
  flex: 1;
  align-items: center;
  img {
    max-height: 15vh;
  }
  svg {
    max-width: 50vw;
    overflow: visible;
  }
`;

const Cell = styled(Box)`
  span {
    margin-right: 8px;
  }
`;

const touchpadSvgs = {
  iq_pro_falcon: (
    <IqProFalcon
      width="auto"
      height="auto"
      viewBox="0 0 750 250"
      preserveAspectRatio="xMidYMid meet"
    />
  ),
  iq_pro_master_16: (
    <IqProMaster16
      width="auto"
      height="auto"
      viewBox="0 0 750 250"
      preserveAspectRatio="xMidYMid meet"
    />
  ),
  iq_pro_master_21: (
    <IqProMaster21
      width="100%"
      height="100%"
      viewBox="0 0 750 300"
      preserveAspectRatio="xMidYMid meet"
    />
  ),
  iq_pro_rotary_slide_14: (
    <IqProRotarySlide14
      width="100%"
      height="100%"
      viewBox="0 0 750 300"
      preserveAspectRatio="xMidYMid meet"
    />
  ),
  iq_pro_slide_17: (
    <IqProSlide17
      width="100%"
      height="100%"
      viewBox="0 0 750 250"
      preserveAspectRatio="xMidYMid meet"
    />
  ),
  iq_pro_rotary_master_15: (
    <IqProRotaryMaster15
      width="100%"
      height="100%"
      viewBox="0 0 750 300"
      preserveAspectRatio="xMidYMid meet"
    />
  ),
  iq_pro_slide_20: (
    <IqProSlide20
      width="100%"
      height="100%"
      viewBox="0 0 750 300"
      preserveAspectRatio="xMidYMid meet"
    />
  ),
};

export function SystemInfo({ info, selectedKey }: SystemInfoProps) {
  return (
    <Card>
      <CardContent>
        <Row>
          <Cell sx={{ flex: 4 }}>
            <Typography variant="h5">Info</Typography>
            <Row>
              <InfoRoot>
                <Cell>
                  <Typography color="text.secondary" component="span">
                    Controller:
                  </Typography>
                  <Typography component="span">
                    {info.controller?.name}
                  </Typography>
                </Cell>
                <Cell>
                  <Typography color="text.secondary" component="span">
                    Touchpad:
                  </Typography>
                  <Typography component="span">
                    {info.touchpad?.name}
                  </Typography>
                </Cell>
                <Cell>
                  <Typography color="text.secondary" component="span">
                    Touchpad keys:
                  </Typography>
                  <Typography component="span">
                    {info.touchpad?.config?.number_of_keys ?? 0}
                  </Typography>
                </Cell>
                <Cell>
                  <Typography color="text.secondary" component="span">
                    Slid-switch steps:
                  </Typography>
                  <Typography component="span">
                    {info.touchpad?.config?.slide_switch_steps ?? "N/A"}
                  </Typography>
                </Cell>
                <Cell>
                  <Typography color="text.secondary" component="span">
                    Rotary-switch steps:
                  </Typography>
                  <Typography component="span">
                    {info.touchpad?.config?.rotary_switch_steps ?? "N/A"}
                  </Typography>
                </Cell>
                <Cell>
                  <Typography color="text.secondary" component="span">
                    Vehicle CAN BUS events:
                  </Typography>
                  <Typography component="span">
                    {info.canBus?.events || "N/A"}
                  </Typography>
                </Cell>
              </InfoRoot>
            </Row>
          </Cell>
          <Cell
            sx={{
              flex: 1,
              [`#key-${selectedKey}`]: {
                fill: "red !important",
              },
            }}
          >
            <Image>
              {!!touchpadSvgs[
                info.touchpad!.id as keyof typeof touchpadSvgs
              ] ? (
                touchpadSvgs[info.touchpad!.id as keyof typeof touchpadSvgs]
              ) : (
                <img src={`${info.touchpad?.image}`} />
              )}
            </Image>
          </Cell>
        </Row>
      </CardContent>
    </Card>
  );
}

interface SystemInfoProps {
  info: SystemInfoInterface;
  selectedKey: number;
}
