import styled from "@emotion/styled";
import {
  Button,
  ToggleButton,
  ToggleButtonGroup,
  Tooltip,
  Typography,
} from "@mui/material";
import Box from "@mui/material/Box";
import KeyboardIcon from "@mui/icons-material/Apps";
import RotarySwitchIcon from "@mui/icons-material/LightMode";
import SlideSwitchIcon from "@mui/icons-material/LinearScale";
import { useEffect, useState } from "react";
import { SequentialTrigger, System, Trigger } from "../../system/interfaces";
import { TriggerInfo } from "./TriggerInfo";
import { TriggeredInfo } from "./TriggeredInfo";
import {useAppDispatch} from "../../../app/hooks";
import {Action} from "@remix-run/router";
import {setSelectedKey} from "../../../app/appSlice";

const Root = styled(Box)`
  display: flex;
  flex: 1 1 auto;
  flex-direction: row;
  margin: 4px;
  padding: 16px;
`;

const KeyListBox = styled(Box)`
  bottom: 0;
  min-width: 200px;
  display: flex;
  flex-direction: column;
`;

const KeyList = styled(Box)`
  display: flex;
  flex: 1;
  flex-direction: column;
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-radius: 4px;
  overflow: scroll;
  padding: 8px;
  margin: 8px;
`;

const ColorCircle = styled(Box)`
  display: inline-block;
  width: 16px;
  height: 16px;
  border-radius: 50px;
  border: 1px solid rbg(0, 0, 0, 0.12);
`;

export function KeysConfiguration({
  system,
  onChange,
}: KeysConfigurationProperty) {
  const dispatch = useAppDispatch();
  const [selectedKey, setSelectedKeyState] = useState(1);
  const [selectedSequence, selectSequence] = useState(0);
  const [selectedCondition, selectCondition] = useState(-1);

  useEffect(() => {
    dispatch(setSelectedKey(selectedKey));
    return () => {
      dispatch(setSelectedKey(0));
    };
  }, []);

  const selectKey = (key: number) => {
    dispatch(setSelectedKey(key));
    setSelectedKeyState(key);
  }


  return (
    <Root>
      <KeysPanel
        numberOfKeys={system.number_of_keys}
        rotarySwitchFirstKey={system.rotary_key_first_position}
        rotarySwitchSteps={system.rotary_switch_steps}
        slideSwitchFirstKey={system.slide_key_first_position}
        slideSwitchSteps={system.slide_switch_steps}
        onKeySelect={selectKey}
        selectedKey={selectedKey}
        items={system.keys}
      />
      <TriggerInfo
        trigger={system.keys.at(selectedKey - 1)}
        numberOfKey={system.number_of_keys}
        numberOfEvent={system.number_of_can_events}
        selectedSequence={selectedSequence}
        onSequenceSelect={selectSequence}
        selectedCondition={selectedCondition}
        onConditionSelect={selectCondition}
        onChange={(t) => {
          if (t.type === 0) {
            t.type = 2;
          }
          system.keys[selectedKey - 1] = t;
          onChange({ ...system });
        }}
      />
      <Box
        sx={{
          width: "300px",
          // border: '1px solid rgba(0, 0, 0, 0.12)',
          borderRadius: "4px",
          margin: "0 16px",
          flex: "4",
          display: "flex",
        }}
      >
        <TriggeredInfo
          system={system}
          selectedTrigger={selectedKey - 1}
          selectedSequence={selectedSequence}
          selectedCondition={selectedCondition}
          onChange={(system) => onChange({ ...system })}
        />
      </Box>
    </Root>
  );
}

function KeysPanel({
  numberOfKeys,
  rotarySwitchFirstKey,
  rotarySwitchSteps,
  slideSwitchFirstKey,
  slideSwitchSteps,
  onKeySelect,
  selectedKey,
  items,
}: KeyListProperty) {
  const [keyFilter, changeKeyFilter] = useState(["key", "rotary", "slide"]);

  const rotarySwitchLastKey = rotarySwitchFirstKey + rotarySwitchSteps - 1;
  const slideSwitchLastKey = slideSwitchFirstKey + slideSwitchSteps - 1;

  const getIcon = (index: number): React.ReactNode => {
    if (index >= rotarySwitchFirstKey && index <= rotarySwitchLastKey) {
      return <RotarySwitchIcon />;
    } else if (index >= slideSwitchFirstKey && index <= slideSwitchLastKey) {
      return <SlideSwitchIcon />;
    }
    return <KeyboardIcon />;
  };

  return (
    <KeyListBox>
      <ToggleButtonGroup
        sx={{
          width: "100%",
          justifyContent: "center",
        }}
        color="primary"
        onChange={(_, filters) => changeKeyFilter(filters)}
        value={keyFilter}
      >
        <ToggleButton value="key">
          <Tooltip title="Normal keys">
            <KeyboardIcon />
          </Tooltip>
        </ToggleButton>
        <ToggleButton value="slide">
          <Tooltip title="Slide switch">
            <SlideSwitchIcon />
          </Tooltip>
        </ToggleButton>
        <ToggleButton value="rotary">
          <Tooltip title="Rotary switch">
            <RotarySwitchIcon />
          </Tooltip>
        </ToggleButton>
      </ToggleButtonGroup>
      <KeyList>
        {items.map((i) => {
          const numberOfNotUsedKeys =
            items.length - numberOfKeys - rotarySwitchSteps - slideSwitchSteps;
          const key = (
            <KeyButton
              index={i.id}
              name={`${(i as Trigger).name ?? ((i as SequentialTrigger).type === 16 ? "Sequential" : "Toogle")} (${
                i.id > numberOfKeys ? i.id - numberOfNotUsedKeys : i.id
              })`}
              onColor={(i as Trigger).on_mode_bg ?? [0, 0, 0]}
              offColor={i.off_bg}
              icon={getIcon(i.id)}
              onClick={() => onKeySelect(i.id)}
              isSelected={selectedKey === i.id}
            />
          );

          if (
            keyFilter.indexOf("rotary") > -1 &&
            i.id >= rotarySwitchFirstKey &&
            i.id <= rotarySwitchLastKey
          ) {
            return key;
          } else if (
            keyFilter.indexOf("slide") > -1 &&
            i.id >= slideSwitchFirstKey &&
            i.id <= slideSwitchLastKey
          ) {
            return key;
          } else if (keyFilter.indexOf("key") > -1 && i.id <= numberOfKeys) {
            return key;
          }
          return <></>;
        })}
      </KeyList>
    </KeyListBox>
  );
}

function KeyButton({
  index,
  name,
  onColor,
  offColor,
  icon,
  onClick,
  isSelected = false,
}: KeyButtonProperty) {
  return (
    <Button
      variant="outlined"
      sx={{
        display: "flex",
        flexDirection: "column !important",
        alignItems: "stretch",
        margin: "4px",
        backgroundColor: isSelected ? "rgba(25, 118, 210, 0.5)" : "",
      }}
      onClick={(_) => !!onClick && onClick()}
    >
      <Box
        sx={{
          display: "flex",
          justifyContent: "space-around",
        }}
      >
        {icon}
        {`${name}`}
      </Box>
      <Box
        sx={{
          display: "flex",
          justifyContent: "center",
        }}
      >
        <ColorCircle sx={{ backgroundColor: `rgb(${onColor.join(",")})` }} />
        <Box sx={{ width: 8 }} />
        <ColorCircle sx={{ backgroundColor: `rgb(${offColor.join(",")})` }} />
      </Box>
    </Button>
  );
}

interface KeysConfigurationProperty {
  system: System;
  onChange(value: System): void;
}

interface KeyListProperty {
  numberOfKeys: number;
  rotarySwitchFirstKey: number;
  rotarySwitchSteps: number;
  slideSwitchFirstKey: number;
  slideSwitchSteps: number;
  onKeySelect: Function;
  selectedKey?: number;
  items: Trigger[] | SequentialTrigger[];
}

interface KeyFilter {
  key?: boolean;
  rotary?: boolean;
  slide?: boolean;
}

interface KeyButtonProperty {
  index: number;
  name: string;
  onColor: number[];
  offColor: number[];
  icon: React.ReactNode;
  onClick?: Function;
  isSelected?: boolean;
}
