const Root = styled(Box)`
  height: 100vh;
  width: 100vw;
`;
const Header = styled(AppBar)`
  flex-direction: row;
  align-items: center;
  justify-const: center;
`;

export function Configuration() {
  const navigate = useNavigate();
  const system = structuredClone(useAppSelector(selectSystem));
  const selectedKey = useAppSelector(selectSelectedKey);
  const dispatch = useAppDispatch();
  const updateSystem = (value: System) => dispatch(
    setSystem(structuredClone(value))
  );
  // const [system, updateSystem] = useState(CONFIGS);

  return (
    <Root>
      <Header position="static">
        <Toolbar>
          <IconButton
            size="large"
            edge="start"
            sx={{ mr: 2 }}
            color="inherit"
            onClick={() => navigate('/system/')}
          >
            <ArrowBack />
          </IconButton>
        </Toolbar>
        <Typography variant="h6" noWrap component="div" sx={{ flexGrow: 1 }}>
          Configuration
        </Typography>
        <Divider />
        <List>
          <ListItem>
            <ListItemButton sx={{ textAlign: 'center' }}>
              <ListItemText primary="Save"
                onClick={()=> generateJsonFile(structuredClone(system))}
              />
            </ListItemButton>
            <ListItemButton sx={{ textAlign: 'center' }}>
              <ListItemText primary="Export to Device"
                onClick={() => generateBinaryFile(structuredClone(system))}
              />
            </ListItemButton>
          </ListItem>
        </List>
      </Header>
      <SystemInfo info={system.info!} selectedKey={selectedKey} />
      <ConfigurationPanel system={system} onChange={updateSystem} />
    </Root>
  );
}
