const appSlice = createSlice({
  name: "app",
  initialState,
  reducers: {
    setInitialState: (state: App, action: PayloadAction<System>) => {
      state.system = action.payload;
    },
    setController: (state: App, action: PayloadAction<Controller>) => {
      state.system.info!.controller = structuredClone(action.payload);
      state.system.number_of_inputs =
        state.system.info!.controller!.config.number_of_inputs;
    },
    setTouchpad: (state, action: PayloadAction<Touchpad>) => {
      state.system.info!.touchpad = structuredClone(action.payload);
      state.system.number_of_total_keys =
        state.system.info!.touchpad.config!.number_of_total_keys;
      state.system.number_of_keys =
        state.system.info!.touchpad.config!.number_of_keys;
      state.system.keyboard_type =
        state.system.info!.touchpad.config!.keyboard_type;
      state.system.number_of_keyboard_led =
        state.system.info!.touchpad.config!.number_of_keyboard_led;
      state.system.first_led_number =
        state.system.info!.touchpad.config!.first_led_number;
      state.system.rotary_switch_steps =
        state.system.info!.touchpad.config!.rotary_switch_steps;
      state.system.rotary_key_first_position =
        state.system.info!.touchpad.config!.rotary_key_first_position;
      state.system.slide_switch_steps =
        state.system.info!.touchpad.config!.slide_switch_steps;
      state.system.slide_key_first_position =
        state.system.info!.touchpad.config!.slide_key_first_position;
      state.system.beeper_timer =
        state.system.info!.touchpad.config!.beeper_timer;

      const numberOfNotUsedKeys =
        state.system.number_of_total_keys -
        state.system.number_of_keys -
        state.system.slide_switch_steps -
        state.system.rotary_switch_steps;
      const numberOfPushKey = state.system.number_of_keys;

      state.system.keys = [
        ...Array(state.system.number_of_total_keys).keys(),
      ].map((k) =>
        getDefaultTrigger(
          k + 1,
          `Key ${(k > numberOfPushKey ? k - numberOfNotUsedKeys : k) + 1}`,
          0
        )
      );
    },
    setVehicle: (state, action: PayloadAction<any>) => {
      state.system.info!.canBus = structuredClone(action.payload);
      state.system.number_of_can_events =
        state.system.info!.canBus?.events ?? 32;
      state.system.can_events = [
        ...Array(state.system.number_of_can_events).keys(),
      ].map((k) => getDefaultTrigger(k + 1, `Event ${k + 1}`, 0));
    },
    setSystem: (state, action: PayloadAction<System>) => {
      state.system = structuredClone(action.payload);
    },
    setSelectedKey: (state, action: PayloadAction<number>) => {
      state.selectedKey = action.payload;
    },
  },
});

export const {
  setInitialState,
  setController,
  setTouchpad,
  setVehicle,
  setSystem,
  setSelectedKey,
} = appSlice.actions;

export const selectController = (state: RootState) =>
  state.app.system.info?.controller;
export const selectTouchpad = (state: RootState) =>
  state.app.system.info?.touchpad;
export const selectVehicle = (state: RootState) =>
  state.app.system.info?.canBus;
export const selectSystem = (state: RootState) => state.app.system;
export const selectSelectedKey = (state: RootState) => state.app.selectedKey;

export default appSlice.reducer;
