import styled from "@emotion/styled";
import {
  Button,
} from "@mui/material";
import Box from "@mui/material/Box";
import { useState } from "react";
import { SequentialTrigger, System, Trigger } from "../../../system/interfaces";
import { TriggerInfo } from "./TriggerInfo";
import { TriggeredInfo } from "./TriggeredInfo";

const Root = styled(Box)`
  display: flex;
  flex: 1 1 auto;
  flex-direction: row;
  margin: 4px;
  padding: 16px;
`;

const AnalogInputListBox = styled(Box)`
  bottom: 0;
  min-width: 200px;
  display: flex;
  flex-direction: column;
`;

const AnalogInputList = styled(Box)`
  display: flex;
  flex: 1;
  flex-direction: column;
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-radius: 4px;
  overflow: scroll;
  padding: 8px;
  margin: 8px;
`;

export function AnalogInputsConfiguration({
  system,
  onChange,
}: AnalogInputsConfigurationProperty) {
  const [selectedAnalogInput, selectAnalogInput] = useState(1);
  const [selectedSequence, selectSequence] = useState(0);
  const [selectedCondition, selectCondition] = useState(-1);

  return (
    <Root>
      <AnalogInputsPanel
        onAnalogInputSelect={selectAnalogInput}
        selectedAnalogInput={selectedAnalogInput}
        items={system.analog_inputs}
      />
      <TriggerInfo
        numberOfKey={system.number_of_keys}
        numberOfEvent={system.number_of_can_events}
        trigger={system.analog_inputs.at(selectedAnalogInput - 1)}
        selectedSequence={selectedSequence}
        onSequenceSelect={selectSequence}
        selectedCondition={selectedCondition}
        onConditionSelect={selectCondition}
        onChange={(t) => {
          system.analog_inputs[selectedAnalogInput - 1] = t;
          onChange({ ...system });
        }}
      />
      <Box
        sx={{
          width: "300px",
          // border: '1px solid rgba(0, 0, 0, 0.12)',
          borderRadius: "4px",
          margin: "0 16px",
          flex: "4",
          display: "flex",
        }}
      >
        <TriggeredInfo
          system={system}
          selectedTrigger={selectedAnalogInput - 1}
          selectedSequence={selectedSequence}
          selectedCondition={selectedCondition}
          onChange={(system) => onChange({ ...system })}
        />
      </Box>
    </Root>
  );
}

function AnalogInputsPanel({
  onAnalogInputSelect,
  selectedAnalogInput,
  items,
}: AnalogInputListProperty) {
  return (
    <AnalogInputListBox
      sx={{
        display: 'none !important',
      }}
    >
      <AnalogInputList>
        {items.map((i) => (
          <AnalogInputButton
            index={i.id}
            name={`${(i as Trigger).name ?? "Sequential"}`}
            onClick={() => onAnalogInputSelect(i.id)}
            isSelected={selectedAnalogInput === i.id}
          />
        ))}
      </AnalogInputList>
    </AnalogInputListBox>
  );
}

function AnalogInputButton({
  index,
  name,
  onClick,
  isSelected = false,
}: AnalogInputButtonProperty) {
  return (
    <Button
      key={index}
      variant="outlined"
      sx={{
        display: "flex",
        flexDirection: "column !important",
        alignItems: "stretch",
        margin: "4px",
        backgroundColor: isSelected ? "rgba(25, 118, 210, 0.5)" : "",
      }}
      onClick={(_) => !!onClick && onClick()}
    >
      <Box
        sx={{
          display: "flex",
          justifyContent: "space-around",
        }}
      >
        {`${name} (${index})`}
      </Box>
    </Button>
  );
}

interface AnalogInputsConfigurationProperty {
  system: System;
  onChange(value: System): void;
}

interface AnalogInputListProperty {
  onAnalogInputSelect: Function;
  selectedAnalogInput?: number;
  items: Trigger[] | SequentialTrigger[];
}

interface AnalogInputButtonProperty {
  index: number;
  name: string;
  onClick?: Function;
  isSelected?: boolean;
}
