import styled from "@emotion/styled";
import {
  Accordion,
  AccordionActions,
  AccordionDetails,
  AccordionSummary,
  Button,
  IconButton,
  MenuItem,
  TextField,
  Typography,
} from "@mui/material";
import Box from "@mui/material/Box";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import DeleteIcon from "@mui/icons-material/Delete";
import { useEffect, useState } from "react";
import {
  Sequence,
  SequentialTrigger,
  Trigger,
  TriggerCondition,
} from "../../system/interfaces";
import { getDefaultTrigger, DEFAULT_TONES } from "./constants";
import { hexToRgb, rgbToHex } from "../../utils/export";
import { useSelector } from "react-redux";
import { selectSystem } from "../../../app/appSlice";

const Root = styled(Box)`
  display: flex;
  flex: 1;
  flex-direction: column;
  overflow: scroll;
  min-width: 260px;
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-radius: 4px;
  padding: 16px;
`;

const Row = styled(Box)`
  display: flex;
  flex-wrap: wrap;
  margin: 8px;
  > div {
    flex: 1;
    margin: 4px;
  }
`;

export function TriggerInfo({
  trigger,
  selectedSequence,
  selectedCondition,
  numberOfKey,
  numberOfEvent,
  onChange,
  onSequenceSelect,
  onConditionSelect,
}: TriggerInfoProperty) {
  const [updatedTrigger, updateTrigger] = useState(trigger);
  const system = useSelector(selectSystem);
  const numberOfNotUsedKeys =
    system.number_of_total_keys -
    system.number_of_keys -
    system.slide_switch_steps -
    system.rotary_switch_steps;

  useEffect(() => {
    updateTrigger(trigger);
  }, [trigger]);

  const handleTriggerChanges = (value: Trigger | SequentialTrigger) => {
    if (
      (updatedTrigger?.type === 16 || updatedTrigger?.type === 6) &&
      value.type !== 16 &&
      value.type !== 6
    ) {
      value = getDefaultTrigger(value.id, `Key ${value.id}`, value.type);
      onSequenceSelect(-1);
    } else if (updatedTrigger?.type !== value.type && value.type === 6) {
      onSequenceSelect(-1);
      value = {
        id: updatedTrigger?.id,
        type: value.type,
        sequences: [
          structuredClone(defaultSequece),
          structuredClone(defaultSequece),
        ],
        off_bg: [0, 0, 255],
        analog_value_show_on_display: 0,
      } as SequentialTrigger;
    }
    // updateTrigger(value);
    onChange(value);
  };

  if (!updatedTrigger) {
    return (
      <Root>
        <Row>
          <Typography variant="h6">Please select a key.</Typography>
        </Row>
      </Root>
    );
  }

  updatedTrigger.type = updatedTrigger.type === 0 ? 2 : updatedTrigger.type;

  return (
    <Root
      sx={{
        padding: "0 16px 16px 16px !important",
      }}
    >
      <Row
        sx={{
          position: "sticky",
          top: 0,
          zIndex: 999,
          backgroundColor: "lightgray",
          borderBottom: "solid",
        }}
      >
        <Typography variant="h6">
          {updatedTrigger.type !== 16 &&
            updatedTrigger.type !== 6 &&
            `Selected Key: ${
              updatedTrigger.id > system.number_of_keys
                ? updatedTrigger.id - numberOfNotUsedKeys
                : updatedTrigger.id
            }`}
          {updatedTrigger.type === 16 &&
            selectedSequence > -1 &&
            `Selected Sequence: ${selectedSequence + 1}`}
          {updatedTrigger.type === 6 &&
            selectedSequence > -1 &&
            `Selected ${selectedSequence === 0 ? "short" : "long"} toggle`}
          <br />
          {updatedTrigger.type !== 16 &&
            updatedTrigger.type !== 6 &&
            selectedCondition > -1 &&
            `Selected Condition: ${selectedCondition + 1}`}
        </Typography>
      </Row>
      <Row>
        <TextField label="Type" select value={updatedTrigger.type}>
          <MenuItem
            value={0}
            selected={updatedTrigger.type === 0}
            onClick={() => handleTriggerChanges({ ...updatedTrigger, type: 0 })}
          >
            Not selected
          </MenuItem>
          <MenuItem
            value={1}
            selected={updatedTrigger.type === 1}
            onClick={() => handleTriggerChanges({ ...updatedTrigger, type: 1 })}
          >
            Momentary
          </MenuItem>
          <MenuItem
            value={6}
            selected={updatedTrigger.type === 6}
            onClick={() => {
              onSequenceSelect(-1);
              handleTriggerChanges({
                id: updatedTrigger.id,
                type: 6,
                sequences: [
                  structuredClone(defaultSequece),
                  structuredClone(defaultSequece),
                ],
                off_bg: updatedTrigger.off_bg,
                analog_value_show_on_display: 0,
              } as SequentialTrigger);
            }}
          >
            Toggle
          </MenuItem>
          <MenuItem
            value={2}
            selected={updatedTrigger.type === 2}
            onClick={() => handleTriggerChanges({ ...updatedTrigger, type: 2 })}
          >
            Short Toggle
          </MenuItem>
          <MenuItem
            value={4}
            selected={updatedTrigger.type === 4}
            onClick={() => handleTriggerChanges({ ...updatedTrigger, type: 4 })}
          >
            Long Toggle
          </MenuItem>
          <MenuItem
            value={8}
            selected={updatedTrigger.type === 8}
            onClick={() => handleTriggerChanges({ ...updatedTrigger, type: 8 })}
          >
            Delay
          </MenuItem>
          <MenuItem
            value={16}
            selected={updatedTrigger.type === 16}
            onClick={() =>
              handleTriggerChanges({
                id: updatedTrigger.id,
                type: 16,
                sequences: [],
                off_bg: updatedTrigger.off_bg,
                analog_value_show_on_display: 0,
              } as SequentialTrigger)
            }
          >
            Sequential
          </MenuItem>
        </TextField>
      </Row>
      <Row
        sx={{
          flexDirection: "column",
          alignItems: "start",
        }}
      >
        <Typography>Background color in off mode</Typography>
        <input
          type="color"
          value={rgbToHex(...updatedTrigger.off_bg)}
          onChange={(event) =>
            handleTriggerChanges({
              ...updatedTrigger,
              off_bg: hexToRgb(event.target.value),
            })
          }
        />
      </Row>
      {(() => {
        if (updatedTrigger.type === 16) {
          return (
            <SequentialTriggerInfo
              trigger={updatedTrigger as SequentialTrigger}
              selectedSequence={selectedSequence}
              onChange={handleTriggerChanges}
              onSequenceSelect={onSequenceSelect}
            />
          );
        } else if (updatedTrigger.type === 6) {
          return (
            <ToggleTriggerInfo
              trigger={updatedTrigger as SequentialTrigger}
              selectedSequence={selectedSequence}
              onChange={handleTriggerChanges}
              onSequenceSelect={onSequenceSelect}
            />
          );
        } else {
          return (
            <NonSequentialTriggerInfo
              trigger={updatedTrigger as Trigger}
              numberOfKey={numberOfKey}
              numberOfEvent={numberOfEvent}
              selectedCondition={selectedCondition}
              onChange={handleTriggerChanges}
              onConditionSelect={onConditionSelect}
            />
          );
        }
      })()}
    </Root>
  );
}

function SequentialTriggerInfo({
  trigger,
  selectedSequence,
  onChange,
  onSequenceSelect,
}: SequentialTriggerInfoProperty) {
  return (
    <>
      <Row>
        <Button
          variant="contained"
          disabled={trigger.sequences.length === 8}
          onClick={(_) => {
            trigger.sequences.push(structuredClone(defaultSequece));
            onChange(structuredClone(trigger));
          }}
        >
          Add Sequence
        </Button>
        <Box
          sx={{
            marginTop: "8px",
          }}
        >
          {trigger.sequences.map((s, i) => (
            <Accordion
              expanded={selectedSequence === i}
              onChange={() => onSequenceSelect(selectedSequence !== i ? i : -1)}
            >
              <AccordionSummary
                expandIcon={<ExpandMoreIcon />}
                id={`sequence-${i}`}
                sx={{
                  justifyContent: "space-around",
                  "> div": {
                    alignItems: "center",
                    justifyContent: "space-around",
                  },
                }}
              >
                <Typography>Sequence {i + 1}</Typography>
                <AccordionActions>
                  <IconButton
                    onClick={(_) => {
                      trigger.sequences.splice(i, 1);
                      onChange({ ...trigger });
                    }}
                  >
                    <DeleteIcon />
                  </IconButton>
                </AccordionActions>
              </AccordionSummary>
              <AccordionDetails>
                <SequenceInfo
                  sequence={s}
                  onChange={(s) => {
                    trigger.sequences[i] = s;
                    onChange({ ...trigger });
                  }}
                />
              </AccordionDetails>
            </Accordion>
          ))}
        </Box>
      </Row>
    </>
  );
}

function ToggleTriggerInfo({
  trigger,
  selectedSequence,
  onChange,
  onSequenceSelect,
}: SequentialTriggerInfoProperty) {
  return (
    <>
      <Row>
        {trigger.sequences.map((s, i) => (
          <Accordion
            expanded={selectedSequence === i}
            onChange={() => onSequenceSelect(selectedSequence !== i ? i : -1)}
          >
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              id={`${i === 0 ? "short-toggle" : "long-toggle"}`}
              key={`${i === 0 ? "short-toggle" : "long-toggle"}`}
              sx={{
                justifyContent: "space-around",
                "> div": {
                  alignItems: "center",
                  justifyContent: "space-around",
                },
              }}
            >
              <Typography>{`${
                i === 0 ? "Short toggle" : "Long toggle"
              }`}</Typography>
              <AccordionActions>
                <IconButton
                  sx={{
                    display: "none !important",
                  }}
                  onClick={(_) => {
                    trigger.sequences.splice(i, 1);
                    onChange({ ...trigger });
                  }}
                >
                  <DeleteIcon />
                </IconButton>
              </AccordionActions>
            </AccordionSummary>
            <AccordionDetails>
              <SequenceInfo
                sequence={s}
                onChange={(s) => {
                  trigger.sequences[i] = s;
                  onChange({ ...trigger });
                }}
              />
            </AccordionDetails>
          </Accordion>
        ))}
      </Row>
    </>
  );
}

function NonSequentialTriggerInfo({
  trigger,
  selectedCondition,
  numberOfKey,
  numberOfEvent,
  onChange,
  onConditionSelect,
}: NonSequentialTriggerInfoProperty) {
  const updateCondition = (keyId: number) => {
    trigger.conditions.at(selectedCondition)!.key_id = keyId;
    onChange(structuredClone(trigger));
  };

  return (
    <>
      <Row
        sx={{
          flexDirection: "column",
          alignItems: "start",
        }}
      >
        <Typography>Background color in on mode</Typography>
        <input
          type="color"
          value={rgbToHex(...trigger.on_mode_bg)}
          onChange={(event) =>
            onChange({ ...trigger, on_mode_bg: hexToRgb(event.target.value) })
          }
        />
      </Row>
      <Row>
        <TextField
          label="Name"
          value={trigger.name}
          onChange={(e) => {
            onChange({ ...trigger, name: e.target.value });
          }}
        />
      </Row>
      <Row
        sx={{
          display: trigger.type === 8 ? "initial" : "none !important",
        }}
      >
        <TextField
          label="Delay (seconds)"
          value={trigger.delay_duration}
          type="number"
          inputProps={{ min: 0, max: 255 }}
          onChange={(e) => {
            onChange({
              ...trigger,
              delay_duration: parseInt(e.target.value ?? 0),
            });
          }}
        />
      </Row>
      <Row>
        <TextField
          label="Priority"
          value={trigger.priority}
          type="number"
          inputProps={{ min: 0, max: 255 }}
          onChange={(e) =>
            onChange({ ...trigger, priority: parseInt(e.target.value ?? 0) })
          }
        />
      </Row>
      <Row>
        <TextField select label="DIMM" value={trigger.dimm_cruse}>
          <MenuItem
            value={0}
            selected={trigger.dimm_cruse === 0}
            onClick={(_) => onChange({ ...trigger, dimm_cruse: 0 })}
          >
            Not used
          </MenuItem>
          <MenuItem
            value={1}
            selected={trigger.dimm_cruse === 1}
            onClick={(_) => onChange({ ...trigger, dimm_cruse: 1 })}
          >
            Used
          </MenuItem>
        </TextField>
        <TextField
          disabled={!trigger.dimm_cruse}
          value={trigger.dimm_cruse_value}
          type="number"
          inputProps={{ min: 0, max: 100 }}
          onChange={(e) =>
            onChange({ ...trigger, dimm_cruse_value: parseInt(e.target.value) })
          }
        />
      </Row>
      <Row>
        <TextField select label="Ignition" value={trigger.ignition}>
          <MenuItem
            value={0}
            selected={trigger.ignition === 0}
            onClick={(_) => onChange({ ...trigger, ignition: 0 })}
          >
            Not used
          </MenuItem>
          <MenuItem
            value={1}
            selected={trigger.ignition === 1}
            onClick={(_) => onChange({ ...trigger, ignition: 1 })}
          >
            Off
          </MenuItem>
          <MenuItem
            value={2}
            selected={trigger.ignition === 2}
            onClick={(_) => onChange({ ...trigger, ignition: 2 })}
          >
            On
          </MenuItem>
        </TextField>
      </Row>
      <Row>
        <TextField select label="Pattern" value={trigger.pattern_sync}>
          <MenuItem
            value={0}
            selected={trigger.pattern_sync === 0}
            onClick={(_) => onChange({ ...trigger, pattern_sync: 0 })}
          >
            Not synced
          </MenuItem>
          <MenuItem
            value={1}
            selected={trigger.pattern_sync === 1}
            onClick={(_) => onChange({ ...trigger, pattern_sync: 1 })}
          >
            Synced
          </MenuItem>
        </TextField>
        <TextField
          value={trigger.pattern_id}
          type="number"
          inputProps={{ min: 0, max: 127 }}
          onChange={(e) =>
            onChange({ ...trigger, pattern_id: parseInt(e.target.value) })
          }
        />
      </Row>
      <Row>
        <TextField
          select
          label="Smart lightbar"
          value={trigger.smart_lightbar_sync}
        >
          <MenuItem
            value={0}
            selected={trigger.smart_lightbar_sync === 0}
            onClick={(_) => onChange({ ...trigger, smart_lightbar_sync: 0 })}
          >
            Not synced
          </MenuItem>
          <MenuItem
            value={1}
            selected={trigger.smart_lightbar_sync === 1}
            onClick={(_) => onChange({ ...trigger, smart_lightbar_sync: 1 })}
          >
            Synced
          </MenuItem>
        </TextField>
        <TextField
          value={trigger.smart_lightbar_id}
          type="number"
          inputProps={{ min: 0, max: 127 }}
          onChange={(e) =>
            onChange({
              ...trigger,
              smart_lightbar_id: parseInt(e.target.value),
            })
          }
        />
      </Row>
      <Row>
        <TextField
          select
          label="Smart side flash"
          value={trigger.smart_side_flash_sync}
        >
          <MenuItem
            value={0}
            selected={trigger.smart_side_flash_sync === 0}
            onClick={(_) => onChange({ ...trigger, smart_side_flash_sync: 0 })}
          >
            Not synced
          </MenuItem>
          <MenuItem
            value={1}
            selected={trigger.smart_side_flash_sync === 1}
            onClick={(_) => onChange({ ...trigger, smart_side_flash_sync: 1 })}
          >
            Synced
          </MenuItem>
        </TextField>
        <TextField
          value={trigger.smart_side_flash_id}
          type="number"
          inputProps={{ min: 0, max: 127 }}
          onChange={(e) =>
            onChange({
              ...trigger,
              smart_side_flash_id: parseInt(e.target.value),
            })
          }
        />
      </Row>
      <Row>
        <TextField
          select
          label="Smart LED message"
          value={trigger.smart_led_message_sync}
        >
          <MenuItem
            value={0}
            selected={trigger.smart_led_message_sync === 0}
            onClick={(_) => onChange({ ...trigger, smart_led_message_sync: 0 })}
          >
            Not synced
          </MenuItem>
          <MenuItem
            value={1}
            selected={trigger.smart_led_message_sync === 1}
            onClick={(_) => onChange({ ...trigger, smart_led_message_sync: 1 })}
          >
            Synced
          </MenuItem>
        </TextField>
        <TextField
          value={trigger.smart_led_message_id}
          type="number"
          inputProps={{ min: 0, max: 127 }}
          onChange={(e) =>
            onChange({
              ...trigger,
              smart_led_message_id: parseInt(e.target.value),
            })
          }
        />
      </Row>
      <Row>
        <TextField
          sx={{
            display: "none !important",
          }}
          select
          label="Tone"
          value={trigger.tone_sync}
        >
          <MenuItem
            value={0}
            selected={trigger.tone_sync === 0}
            onClick={(_) => onChange({ ...trigger, tone_sync: 0 })}
          >
            Not synced
          </MenuItem>
          <MenuItem
            value={1}
            selected={trigger.tone_sync === 1}
            onClick={(_) => onChange({ ...trigger, tone_sync: 1 })}
          >
            Synced
          </MenuItem>
        </TextField>
        <TextField
          select
          label="Tone"
          value={trigger.tone_id}
          onChange={(e) =>
            onChange({ ...trigger, tone_id: parseInt(e.target.value) })
          }
        >
          {
            [
            ...DEFAULT_TONES.map((v, i) => {
              return <MenuItem
                value={i}
                selected={trigger.tone_id === i}
                onClick={(_) => onChange({ ...trigger, tone_id: i})}
              >
                {v}
              </MenuItem>
            }
            ),
            ...Object.keys([...new Array(3)]).map((_, i) => {
              return <MenuItem
                value={i + 7}
                selected={trigger.tone_id === i + 7}
                onClick={(_) => onChange({ ...trigger, tone_id: i + 7 })}
              >
                Reserved
              </MenuItem>
            }
            ),
            ...Object.keys([...new Array(20)]).map((_, i) => {
              return <MenuItem
                value={i + 11}
                selected={trigger.tone_id === i + 11}
                onClick={(_) => onChange({ ...trigger, tone_id: i + 11 })}
              >
                Custom - T{i + 11}
              </MenuItem>
            }
            )
          ]
          }
        </TextField>
      </Row>
      <Row>
        <TextField select label="Horn ring loop" value={trigger.horn_ring_loop}>
          <MenuItem
            value={0}
            selected={trigger.horn_ring_loop === 0}
            onClick={(_) => onChange({ ...trigger, horn_ring_loop: 0 })}
          >
            No change
          </MenuItem>
          <MenuItem
            value={1}
            selected={trigger.horn_ring_loop === 1}
            onClick={(_) => onChange({ ...trigger, horn_ring_loop: 1 })}
          >
            Off
          </MenuItem>
          <MenuItem
            value={2}
            selected={trigger.horn_ring_loop === 2}
            onClick={(_) => onChange({ ...trigger, horn_ring_loop: 2 })}
          >
            On
          </MenuItem>
        </TextField>
      </Row>
      <Row>
        <Button
          variant="contained"
          disabled={trigger.conditions.length === 8}
          onClick={(_) => {
            trigger.conditions.push(structuredClone(defaultCondition));
            onChange(structuredClone(trigger));
          }}
        >
          Add Condition
        </Button>
        <Box
          sx={{
            marginTop: "8px",
          }}
        >
          {trigger.conditions.map((s, i) => (
            <Accordion
              expanded={selectedCondition === i}
              onChange={() =>
                onConditionSelect(selectedCondition !== i ? i : -1)
              }
            >
              <AccordionSummary
                expandIcon={<ExpandMoreIcon />}
                id={`condition-${i}`}
                sx={{
                  justifyContent: "space-around",
                  "> div": {
                    alignItems: "center",
                    justifyContent: "space-around",
                  },
                }}
              >
                <Typography>Condition {i + 1}</Typography>
                <AccordionActions>
                  <IconButton
                    onClick={(_) => {
                      trigger.conditions.splice(i, 1);
                      onChange(structuredClone(trigger));
                    }}
                  >
                    <DeleteIcon />
                  </IconButton>
                </AccordionActions>
              </AccordionSummary>
              <AccordionDetails>
                <Row>
                  <Typography>If current key and key No.</Typography>
                </Row>
                <Row>
                  <TextField
                    select
                    value={
                      trigger.conditions.at(selectedCondition)?.key_id ?? 0
                    }
                  >
                    {[
                      <MenuItem value={0}>Select A Key</MenuItem>,
                      ...[...Array(numberOfKey).keys()].map((k) => (
                        <MenuItem
                          value={k + 1}
                          onClick={() => updateCondition(k + 1)}
                        >
                          Key {k + 1}
                        </MenuItem>
                      )),
                      ...[...Array(14).keys()].map((k) => (
                        <MenuItem
                          value={k + 33}
                          onClick={() => updateCondition(k + 33)}
                        >
                          Input {k + 1}
                        </MenuItem>
                      )),
                      ...[...Array(2).keys()].map((k) => (
                        <MenuItem
                          value={k + 47}
                          onClick={() => updateCondition(k + 47)}
                        >
                          Analog Input {k + 1}
                        </MenuItem>
                      )),
                      <MenuItem value={49} onClick={() => updateCondition(46)}>
                        Horn Ring
                      </MenuItem>,
                      ...[...Array(16).keys()].map((k) => (
                        <MenuItem
                          value={k + 50}
                          onClick={() => updateCondition(k + 50)}
                        >
                          Virtual Key {k + 1}
                        </MenuItem>
                      )),
                      ...[...Array(numberOfEvent).keys()].map((k) => (
                        <MenuItem
                          value={k + 66}
                          onClick={() => updateCondition(k + 66)}
                        >
                          CAN Event {k + 1}
                        </MenuItem>
                      )),
                    ]}
                  </TextField>
                </Row>
              </AccordionDetails>
            </Accordion>
          ))}
        </Box>
      </Row>
    </>
  );
}

