import React, { memo } from "react";
import styled from "@emotion/styled";
import { ArrowBack } from "@mui/icons-material";
import {
  AppBar,
  Box,
  Button,
  IconButton,
  Step,
  StepButton,
  Stepper,
  Toolbar,
  Typography,
} from "@mui/material";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { AllConfigs } from "./components/AllConfigs";
import { CanBusSelector } from "./components/CanBusSelector";
import { ControllerSelector } from "./components/ControllerSelector";
import { TouchpadSelector } from "./components/TouchpadSelector";
import { Step as StepType } from "./interfaces";
import { useAppSelector } from "../../app/hooks";
import { selectSystem } from "../../app/appSlice";

const Root = styled(Box)`
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
`;

const Card = styled.div`
  border: solid 1px;
  border-radius: 4px;
  width: 80vw;
  min-width: 350px;
  margin: 16px;
`;
const CardBody = styled.div`
  flex-grow: 1;
  min-height: 40vh;
  padding: 8px;
  margin: 8px;
`;

const Header = styled(AppBar)`
  flex-direction: row;
  align-items: center;
  justify-const: center;
`;

const steps: StepType[] = [
  {
    index: 1,
    name: "Controller",
    Component: ControllerSelector,
  },
  {
    index: 2,
    name: "Touchpad",
    Component: TouchpadSelector,
  },
  {
    index: 3,
    name: "Vehicle",
    Component: CanBusSelector,
  },
  {
    index: 4,
    name: "Configs",
    Component: AllConfigs,
  },
];

export function SystemConfig() {
  const system = useAppSelector(selectSystem);
  const navigate = useNavigate();
  const [completedStep, setCompletedStep] = useState<{ [k: number]: boolean }>(
    {}
  );
  const [activeStep, setActiveStep] = useState(steps.at(0));

  const SelectedComponent =
    activeStep?.Component ?? steps.at(0)!.Component;

  const totalSteps = () => {
    return steps.length;
  };

  const completedSteps = () => {
    return Object.keys(completedStep).length;
  };

  const isLastStep = () => {
    return activeStep?.index === totalSteps();
  };

  const allStepsCompleted = () => {
    return completedSteps() === totalSteps();
  };

  const handleNext = () => {
    // isLastStep() && !allStepsCompleted()
    //   ? // It's the last step, but not all steps have been completed,
    // find the first step that has been completed
    // steps.find(s => !completedStep[s.index])
    if (isLastStep()) {
      navigate("../configuration");
      return;
    }

    const newActiveStep = steps.find(
      (s) => s.index === (activeStep?.index ?? 0) + 1
    );
    setActiveStep(newActiveStep);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) =>
      steps.find((s) => s.index === (prevActiveStep?.index ?? 2) - 1)
    );
  };

  const handleStep = (step: StepType) => () => {
    setActiveStep(step);
  };
  const handleComplete = () => {
    const newCompleted = completedStep;
    newCompleted[activeStep?.index ?? 1] = true;
    setCompletedStep(newCompleted);
    handleNext();
  };

  const handleReset = () => {
    setActiveStep(steps[0]);
    setCompletedStep({});
  };

  const getNextBtnVisibility = () => {
    switch (activeStep?.index) {
      case 1:
        return !system.info?.controller;
      case 2:
        return !system.info?.touchpad;
      case 3:
        return false;
      default:
        return false;
    }
  };

  return (
    <Root>
      <Card>
        <Header position="static">
          <Toolbar>
            <IconButton
              size="large"
              edge="start"
              sx={{ mr: 2 }}
              color="inherit"
              onClick={() => navigate(-1)}
            >
              <ArrowBack />
            </IconButton>
          </Toolbar>
          <Typography variant="h6" noWrap component="div" sx={{ flexGrow: 1 }}>
            System Configuration
          </Typography>
        </Header>
        <CardBody>
          <Stepper activeStep={(activeStep?.index ?? 1) - 1}>
            {steps.map((content, index) => (
              <Step key={content.name} completed={completedStep[content.index]}>
                <StepButton color="inherit" onClick={handleStep(content)}>
                  {content.name}
                </StepButton>
              </Step>
            ))}
          </Stepper>
          <div>
            {allStepsCompleted() ? (
              <>
                <Typography sx={{ mt: 2, mb: 1 }}>
                  All steps completed - you&apos;re finished
                </Typography>
                <Box sx={{ display: "flex", flexDirection: "row", pt: 2 }}>
                  <Box sx={{ flex: "1 1 auto" }} />
                  <Button onClick={handleReset}>Reset</Button>
                </Box>
              </>
            ) : (
              <>
                <>
                  <SelectedComponent />
                </>
                <Box sx={{ display: "flex", flexDirection: "row", pt: 2 }}>
                  <Button
                    color="inherit"
                    disabled={activeStep?.index === 1}
                    onClick={handleBack}
                    sx={{ mr: 1 }}
                  >
                    Back
                  </Button>
                  <Box sx={{ flex: "1 1 auto" }} />
                  <Button
                    disabled={getNextBtnVisibility()}
                    onClick={handleNext}
                    sx={{ mr: 1 }}
                  >
                    Next
                  </Button>
                  {
                    // activeStep?.index !== steps.length &&
                    // (completedStep[activeStep?.index??1] ? (
                    //   <Typography variant="caption" sx={{ display: 'inline-block' }}>
                    //     Step {activeStep?.index} already completed
                    //   </Typography>
                    // ) : (
                    //   <Button onClick={handleComplete}>
                    //     {completedSteps() === totalSteps() - 1
                    //       ? 'Finish'
                    //       : 'Complete Step'}
                    //     </Button>
                    // ))
                  }
                </Box>
              </>
            )}
          </div>
        </CardBody>
      </Card>
    </Root>
  );
}
