const Image = styled.img`
  width: 15vw;
  aspect-ratio: 2.65;
`;

export function TouchpadSelector() {
  const selectedTouchpad = useAppSelector(selectTouchpad);
  const dispatch = useAppDispatch();

  return (
    <Root id="controller-selector-root" key="Touchpad">
      {touchpads.map((c) => (
        <ImageButton
          value={c}
          selected={selectedTouchpad?.id === c.id}
          onChange={() => {
            dispatch(setTouchpad(c));
          }}
        >
          <Image src={`${c.image}`} />
          <h5>{c.name}</h5>
        </ImageButton>
      ))}
    </Root>
  );
}
